import React, { useState } from "react";
import API from "../../config/config";
import { toast } from "react-toastify";

export default function StepAadhaar({
  formData,
  setFormData,
  driver,
  nextStep,
  prevStep,
}) {
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem("adminToken");

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "aadhaarNumber") {
      // Allow only digits and limit to 12 characters
      const numericValue = value.replace(/\D/g, "");
      if (numericValue.length <= 12) {
        setFormData((prev) => ({ ...prev, [name]: numericValue }));
      }
      return;
    }

    if (name === "nameToMatch") {
      // Allow only alphabets and space
      const alphaValue = value.replace(/[^A-Za-z\s]/g, "");
      setFormData((prev) => ({ ...prev, [name]: alphaValue }));
      return;
    }
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e, key) => {
    const file = e.target.files[0];
    if (file) {
      setFormData((prev) => ({ ...prev, [key]: file }));
    }
  };

  const uploadFile = async (file, type) => {
    const headers = new Headers();
    headers.append("Authorization", "Bearer " + token);

    const formDataUpload = new FormData();
    formDataUpload.append("documentFor", "Driver");
    formDataUpload.append("type", type);
    formDataUpload.append("ownerId", driver._id);
    formDataUpload.append("file", file, file.name);

    const response = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
      method: "POST",
      headers,
      body: formDataUpload,
    });

    if (!response.ok) {
      throw new Error(`${type} upload failed`);
    }
    const data = await response.json();
    return data.document?.documentUrl;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { aadhaarNumber, nameToMatch, aadhaarFront, aadhaarBack } = formData;
    if (!aadhaarNumber || !nameToMatch || !aadhaarFront || !aadhaarBack) {
      toast.warn("Please fill all fields and upload both Aadhaar images.");
      return;
    }

    setLoading(true);
    try {
      const frontUrl =
        typeof aadhaarFront === "string"
          ? aadhaarFront
          : await uploadFile(aadhaarFront, "aadhaar_front");

      const backUrl =
        typeof aadhaarBack === "string"
          ? aadhaarBack
          : await uploadFile(aadhaarBack, "aadhaar_back");

      const payload = {
        ownerId: driver._id,
        aadhaarNumber,
        nameToMatch: nameToMatch,
        consent: "Y",
        consentText:
          "I hereby declare my consent agreement for fetching my information via ZOOP API",
        taskId: "08b01aa8-9487-4e6d-a0f0-c796839d6b77",
        aadhaarfront: frontUrl,
        aadhaarback: backUrl,
      };

      const response = await fetch(API.ADMIN.ADMIN_VERIFY_AADHAAR, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + token,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      const result = await response.json();

      if (response.ok && result.success) {
        toast.success("Aadhaar submitted successfully");
        setFormData((prev) => ({
          ...prev,
          aadhaarFront: frontUrl,
          aadhaarBack: backUrl,
          nameToMatch,
          aadhaarNumber,
        }));
        nextStep();
      } else {
        toast.error(result.message || "Aadhaar submission failed");
      }
    } catch (error) {
      console.error("Aadhaar Submit Error:", error);
      toast.error("Submission failed");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-4xl mx-auto p-4 bg-white rounded shadow text-sm"
    >
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block mb-1 text-gray-700 font-medium text-sm">
            Aadhaar Number
          </label>
          <input
            type="text"
            name="aadhaarNumber"
            value={formData.aadhaarNumber || ""}
            onChange={handleChange}
            placeholder="Enter Aadhaar Number"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
        <div>
          <label className="block mb-1 text-gray-700 font-medium text-sm">
            Name on Aadhaar
          </label>
          <input
            type="text"
            name="nameToMatch"
            value={formData.nameToMatch || ""}
            onChange={handleChange}
            placeholder="Enter Name as on Aadhaar"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
        <div>
          <label className="block mb-1 text-gray-700 font-medium text-sm">
            Upload Aadhaar Front
          </label>
          <input
            type="file"
            accept="image/*,application/pdf"
            onChange={(e) => handleFileChange(e, "aadhaarFront")}
            className="w-full border px-2 py-1 rounded text-sm"
            required={!formData.aadhaarFront}
          />
          {formData.aadhaarFront && (
            <img
              src={
                typeof formData.aadhaarFront === "string"
                  ? formData.aadhaarFront
                  : URL.createObjectURL(formData.aadhaarFront)
              }
              alt="Aadhaar Front"
              className="w-24 mt-1 border rounded"
            />
          )}
        </div>
        <div>
          <label className="block mb-1 text-gray-700 font-medium text-sm">
            Upload Aadhaar Back
          </label>
          <input
            type="file"
            accept="image/*,application/pdf"
            onChange={(e) => handleFileChange(e, "aadhaarBack")}
            className="w-full border px-2 py-1 rounded text-sm"
            required={!formData.aadhaarBack}
          />
          {formData.aadhaarBack && (
            <img
              src={
                typeof formData.aadhaarBack === "string"
                  ? formData.aadhaarBack
                  : URL.createObjectURL(formData.aadhaarBack)
              }
              alt="Aadhaar Back"
              className="w-24 mt-1 border rounded"
            />
          )}
        </div>
      </div>
      <div className="mt-4">
        <button
          type="submit"
          className={`w-full bg-indigo-600 text-white py-2 rounded text-sm ${
            loading ? "opacity-60 cursor-not-allowed" : ""
          }`}
          disabled={loading}
        >
          {loading ? "Submitting..." : "Submit & Continue"}
        </button>
      </div>
    </form>
  );
}
