import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useLocation,
} from "react-router-dom";
import Dashboard from "./pages/Dashboard";
import Drivers from "./pages/Drivers";
import Admins from "./pages/Admins";
import Rides from "./pages/Rides";
import Vehicles from "./pages/Vehicles";
import Payments from "./pages/Payments";
import Login from "./pages/Login";
import AdminPrivateRoute from "./components/AdminPrivateRoute";
import DriverPrivateRoute from "./components/Driver/DriverPrivateRoute";
import DriverDetail from "./pages/DriverDetail";
import Users from "./pages/Users";
import VehicleTypes from "./pages/VehicleTypes";
import Brands from "./pages/Brands";
import Employees from "./pages/Employees";
import AddEmployee from "./pages/AddEmployee";
import Settings from "./pages/Settings";
import Dropdown from "./components/Dropdown";
import Notification from "./components/Notifications";
import ToggleSidebar from "./components/ToggleSidebar";
import Roles from "./pages/Roles";
import AddAdmin from "./pages/AddAdmin";
import Issues from "./pages/Issues";
import RateCard from "./pages/RateCard";
import FaceLiveness from "./pages/FaceLiveness";
import RequestOTP from "./pages/Driver/RequestOTP";
import AdminSidebar from "./components/AdminSidebar";
import UserSidebar from "./components/Driver/UserSidebar";
import VerifyOTP from "./pages/Driver/VerifyOTP";
import UserDashboard from "./pages/Driver/UserDashboard";
import UploadProfilePic from "./pages/Driver/UploadProfilePic";
import VerifyLicense from "./pages/Driver/VerifyLicense";
import VerifyPancard from "./pages/Driver/VerifyPancard";
import VerifyAaadhar from "./pages/Driver/VerifyAaadhar";
import DrivingChoices from "./pages/Driver/DrivingChoices";
import UploadDrivingVideos from "./pages/Driver/UploadDrivingVideos";
import AddDriver from "./pages/AddDriver";
import CreateDriverStart from "./pages/CreateDriverStart";
import Profile from "./pages/Profile";
import { ProfileProvider } from "./context/ProfileContext";
import VerifyBankAccount from "./pages/Driver/VerifyBankAccount";

function App() {
  return (
    <ProfileProvider>
      <Router>
        <AppRoutes />
      </Router>
    </ProfileProvider>
  );
}

const AppRoutes = () => {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const location = useLocation();
  const currentPath = location.pathname;

  const adminPaths = [
    "/",
    "/dashboard",
    "/admins",
    "/drivers",
    "/users",
    "/employees",
    "/vehicles",
    "/payments",
    "/vehicle-types",
    "/vehicles/brands",
    "/settings",
    "/roles",
    "/issues",
    "/ratecard",
    "/drivers/detail",
    "/profile",
  ];

  const userPaths = [
    "/user/dashboard",
    "/user/profile",
    "/user/rides",
    "/user/wallet",
    // Add any other exact user routes here
  ];

  console.log(currentPath);

  const isAdminRoute = adminPaths.some((base) => {
    const regex = new RegExp(`^${base}(/.*)?$`); // match base or base + subpath
    return regex.test(currentPath);
  });
  const isUserRoute = userPaths.includes(currentPath);

  useEffect(() => {
    const timer = setTimeout(() => {}, 500);
    return () => clearTimeout(timer);
  }, [location]);

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  return (
    <div className="flex h-screen overflow-hidden">
      {/* Sidebars */}
      {isAdminRoute && <AdminSidebar open={sidebarOpen} />}
      {isUserRoute && <UserSidebar open={sidebarOpen} />}

      {/* Main Content */}
      <div className="flex flex-col flex-1 w-full overflow-y-auto overflow-x-hidden">
        {/* Header */}
        {(isAdminRoute || isUserRoute) && (
          <header className="sticky top-0 z-40 flex w-full bg-white shadow-md justify-between items-center px-4 py-3">
            <ToggleSidebar click={toggleSidebar} />
            <div className="flex items-center space-x-4">
              <Notification />
              <Dropdown />
            </div>
          </header>
        )}

        {/* Routes */}
        <Routes>
          <Route path="/face-liveness" element={<FaceLiveness />} />
          <Route path="/login" element={<Login />} />
          <Route path="/request-otp" element={<RequestOTP />} />
          <Route path="/verify-otp" element={<VerifyOTP />} />
          <Route path="/dashboard" element={<Dashboard />} />
          {/* User Protected Routes */}
          <Route
            path="/user-dashboard"
            element={
              <DriverPrivateRoute>
                <UserDashboard />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/upload-profile"
            element={
              <DriverPrivateRoute>
                <UploadProfilePic />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/verify-license"
            element={
              <DriverPrivateRoute>
                <VerifyLicense />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/verify-pan"
            element={
              <DriverPrivateRoute>
                <VerifyPancard />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/verify-bank"
            element={
              <DriverPrivateRoute>
                <VerifyBankAccount />
              </DriverPrivateRoute>
            }
          />

          <Route
            path="/verify-aadhar"
            element={
              <DriverPrivateRoute>
                <VerifyAaadhar />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/driving-choices"
            element={
              <DriverPrivateRoute>
                <DrivingChoices />
              </DriverPrivateRoute>
            }
          />
          <Route
            path="/driving-videos"
            element={
              <DriverPrivateRoute>
                <UploadDrivingVideos />
              </DriverPrivateRoute>
            }
          />
          {/* Admin Protected Routes */}
          <Route
            path="/"
            element={
              <AdminPrivateRoute>
                <Dashboard />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/profile"
            element={
              <AdminPrivateRoute>
                <Profile />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/drivers"
            element={
              <AdminPrivateRoute>
                <Drivers />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/drivers/add"
            element={
              <AdminPrivateRoute>
                <CreateDriverStart />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/drivers/add/:id"
            element={
              <AdminPrivateRoute>
                <AddDriver />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/users"
            element={
              <AdminPrivateRoute>
                <Users />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/employees"
            element={
              <AdminPrivateRoute>
                <Employees />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/employees/add"
            element={
              <AdminPrivateRoute>
                <AddEmployee />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/admins"
            element={
              <AdminPrivateRoute>
                <Admins />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/admins/add"
            element={
              <AdminPrivateRoute>
                <AddAdmin />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/rides/:status"
            element={
              <AdminPrivateRoute>
                <Rides />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/vehicles"
            element={
              <AdminPrivateRoute>
                <Vehicles />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/vehicle-types"
            element={
              <AdminPrivateRoute>
                <VehicleTypes />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/vehicles/brands"
            element={
              <AdminPrivateRoute>
                <Brands />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/payments"
            element={
              <AdminPrivateRoute>
                <Payments />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/drivers/detail/:id"
            element={
              <AdminPrivateRoute>
                <DriverDetail />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/settings"
            element={
              <AdminPrivateRoute>
                <Settings />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/roles"
            element={
              <AdminPrivateRoute>
                <Roles />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/issues"
            element={
              <AdminPrivateRoute>
                <Issues />
              </AdminPrivateRoute>
            }
          />
          <Route
            path="/ratecard"
            element={
              <AdminPrivateRoute>
                <RateCard />
              </AdminPrivateRoute>
            }
          />
        </Routes>
      </div>
    </div>
  );
};

export default App;
