import { useState, useEffect } from "react";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";
import { Snackbar, Alert } from "@mui/material";

const Roles = ({ roleId = null }) => {
  const [roles, setRoles] = useState([]);
  const [slug, setSlug] = useState("");
  const [roleName, setRoleName] = useState("");
  const [modules, setModules] = useState([]);
  const [permissions, setPermissions] = useState({});
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [popup, setPopup] = useState({
    open: false,
    message: "",
    severity: "",
  });

  useEffect(() => {
    const fetchRolesAndModules = async () => {
      setLoading(true);
      setError("");
      try {
        const token = localStorage.getItem("adminToken");

        // Fetch roles
        const rolesResponse = await axiosInstance.get(API.ADMIN.ROLES_LIST, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setRoles(rolesResponse.data.roles);

        // Fetch modules
        const moduleResponse = await axiosInstance.get(API.ADMIN.MODULES_LIST, {
          headers: { Authorization: `Bearer ${token}` },
        });
        const modulesData = moduleResponse.data.modules;

        // Initialize permissions
        const permissionsFromModules = modulesData.reduce((acc, module) => {
          acc[module._id] = {
            add: false,
            edit: false,
            view: false,
            delete: false,
          };
          return acc;
        }, {});

        setModules(modulesData);

        // If a role is selected, fetch its details
        if (roleId) {
          const roleResponse = await axiosInstance.get(
            `${API.ADMIN.ROLE_DETAILS}/${roleId}`,
            {
              headers: { Authorization: `Bearer ${token}` },
            }
          );
          const roleData = roleResponse.data;

          setSlug(roleData.slug);
          setRoleName(roleData.name);

          // Update permissions with role-specific actions
          const updatedPermissions = modulesData.reduce((acc, module) => {
            const rolePermission = roleData.permissions.find(
              (perm) => perm.module._id === module._id
            );
            acc[module._id] = rolePermission
              ? rolePermission.actions
              : { add: false, edit: false, view: false, delete: false };
            return acc;
          }, {});

          setPermissions(updatedPermissions);
        } else {
          setPermissions(permissionsFromModules);
        }
      } catch (error) {
        setError("Error fetching roles or modules.");
        console.error("Error fetching roles or modules:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchRolesAndModules();
  }, [roleId]);

  const handleRoleChange = async (selectedRoleId) => {
    if (selectedRoleId) {
      try {
        const token = localStorage.getItem("adminToken");

        // Fetch role details
        const roleResponse = await axiosInstance.get(
          `${API.ADMIN.ROLE_DETAILS}/${selectedRoleId}`,
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );
        const roleData = roleResponse.data;
        console.log(roleData);
        setSlug(roleData.slug);
        setRoleName(roleData.name);
        // Update permissions based on role details
        const updatedPermissions = modules.reduce((acc, module) => {
          const rolePermission = roleData.permissions.find(
            (perm) => perm.module._id === module._id
          );
          acc[module._id] = rolePermission
            ? rolePermission.actions
            : { add: false, edit: false, view: false, delete: false };
          return acc;
        }, {});

        setPermissions(updatedPermissions);
      } catch (error) {
        setError("Error fetching role details.");
        console.error("Error fetching role details:", error);
      }
    } else {
      // Reset permissions to default if no role is selected
      const defaultPermissions = modules.reduce((acc, module) => {
        acc[module._id] = {
          add: false,
          edit: false,
          view: false,
          delete: false,
        };
        return acc;
      }, {});
      setPermissions(defaultPermissions);
    }
  };

  const handleCheckboxChange = (moduleId, action) => {
    setPermissions((prevState) => ({
      ...prevState,
      [moduleId]: {
        ...prevState[moduleId],
        [action]: !prevState[moduleId][action],
      },
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError("");

    if (!slug) {
      setError("Role slug is required.");
      return;
    }

    console.log(permissions);

    const roleData = {
      name: roleName, // Add this
      slug,
      permissions: Object.keys(permissions).map((moduleId) => ({
        module: moduleId,
        actions: permissions[moduleId],
      })),
    };

    try {
      const token = localStorage.getItem("adminToken");
      await axiosInstance.post(API.ADMIN.CREATEROLEPERMISSION, roleData, {
        headers: { Authorization: `Bearer ${token}` },
      });

      setPopup({
        open: true,
        message: "Permissions saved successfully!",
        severity: "success",
      });
    } catch (error) {
      setError("Error saving permissions.");
      console.error("Error saving permissions:", error);
    }
  };

  const handleClosePopup = () => {
    setPopup({ ...popup, open: false });
  };

  return (
    <div className="p-6 bg-gray-100">
      <h1 className="text-2xl font-bold mb-4">Role Details</h1>
      {loading && <p>Loading...</p>}
      {error && <p className="text-red-500">{error}</p>}

      <div>
        <label className="block font-semibold mb-1">Role:</label>
        <select
          onChange={(e) => handleRoleChange(e.target.value)}
          className="w-full p-2 border rounded"
        >
          <option value="">Select a Role</option>
          {roles.map((role) => (
            <option key={role._id} value={role._id}>
              {role.name}
            </option>
          ))}
        </select>
      </div>

      <form onSubmit={handleSubmit} className="mt-6">
        <div className="bg-white p-4 shadow rounded">
          <h2 className="text-xl font-bold mb-4">Permissions</h2>
          {modules.map((module) => (
            <div key={module._id} className="mb-4">
              <h3 className="text-lg font-semibold mb-2">
                {module.name.charAt(0).toUpperCase() + module.name.slice(1)}
              </h3>
              <div className="grid grid-cols-4 gap-2">
                {["add", "edit", "view", "delete"].map((action) => (
                  <label key={action} className="flex items-center gap-2">
                    <input
                      type="checkbox"
                      checked={permissions[module._id]?.[action] || false}
                      onChange={() => handleCheckboxChange(module._id, action)}
                      className="rounded"
                    />
                    {action.charAt(0).toUpperCase() + action.slice(1)}
                  </label>
                ))}
              </div>
            </div>
          ))}
        </div>

        <button
          type="submit"
          className="mt-4 px-4 py-2 bg-blue-600 text-white rounded shadow hover:bg-blue-700"
        >
          Save Permissions
        </button>
        {/* Snackbar Popup */}
        <Snackbar
          open={popup.open}
          autoHideDuration={6000}
          onClose={handleClosePopup}
          anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
        >
          <Alert
            onClose={handleClosePopup}
            severity={popup.severity}
            sx={{ width: "100%" }}
          >
            {popup.message}
          </Alert>
        </Snackbar>
      </form>
    </div>
  );
};

export default Roles;
