import React, { useEffect, useState } from "react";
import { CSVLink } from "react-csv";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";
import DataTable from "../components/DataTable";

const Users = () => {
  const [users, setUsers] = useState([]);
  const [filteredUsers, setFilteredUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(API.ADMIN.USERS_LIST, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setUsers(response.data.users);
        setFilteredUsers(response.data.users);
      } catch (error) {
        console.error("Error fetching users:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchUsers();
  }, []);

  const handleFilter = () => {
    if (!startDate || !endDate) {
      alert("Please select valid date ranges to filter!");
      return;
    }

    const filteredData = users.filter((user) => {
      const createdAt = new Date(user.createdAt);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return createdAt >= start && createdAt <= end;
    });

    setFilteredUsers(filteredData);
  };

  const csvHeaders = [
    { label: "User ID", key: "userId" },
    { label: "Name", key: "fullName" },
    { label: "Phone", key: "contactNumber" },
    { label: "Email", key: "email" },
    { label: "Created At", key: "createdAt" },
  ];

  const columns = [
    { Header: "User ID", accessor: "code" },
    { Header: "Name", accessor: "fullName" },
    { Header: "Phone", accessor: "phone" },
    { Header: "Email", accessor: "email" },
    { Header: "Created At", accessor: "createdAt" },
  ];

  return (
    <div className="container mx-auto px-6 py-8 bg-gray-100 min-h-screen">
      {/* Header Section */}
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-3xl font-bold text-gray-800">
          Customer Management
        </h2>
      </div>

      {/* Filter Section */}
      <div className="bg-white shadow-md rounded-lg px-6 py-4 mb-6 border border-gray-200">
        <div className="flex items-center space-x-4">
          <div>
            <label className="text-sm text-gray-600 block mb-1">
              Start Date
            </label>
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>
          <div>
            <label className="text-sm text-gray-600 block mb-1">End Date</label>
            <input
              type="date"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>
          <button
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg shadow-md transition duration-200 ease-in-out focus:outline-none"
            onClick={handleFilter}
          >
            Apply Filter
          </button>
        </div>
      </div>

      {/* Data Table Section */}
      <div className="bg-white shadow-md rounded-lg px-4 py-6 border border-gray-200">
        <DataTable
          data={filteredUsers}
          columns={columns}
          loading={loading}
          addLink="/users/add"
          editPath="/users/edit"
          detailPath="/users/detail"
        />
      </div>
    </div>
  );
};

export default Users;
