import { useEffect, useRef, useState } from "react";
import { Link } from 'react-router-dom';

const Notification = () => {
  const ref = useRef();

  const [isMenuOpen, setIsMenuOpen] = useState(false);

  useEffect(() => {
    const checkIfClickedOutside = (e) => {
      // If the menu is open and the clicked target is not within the menu,
      // then close the menu
      if (isMenuOpen && ref.current && !ref.current.contains(e.target)) {
        setIsMenuOpen(false);
      }
    };

    document.addEventListener("mousedown", checkIfClickedOutside);
    return () => {
      // Cleanup the event listener
      document.removeEventListener("mousedown", checkIfClickedOutside);
    };
  }, [isMenuOpen]); 

    return (
        <div className="relative inline-block text-left" ref={ref}>

            <div className='mr-1' >
                <button
                    onClick={() => setIsMenuOpen((oldState) => !oldState)}
                    className="inline-flex justify-center w-full rounded-full 
                    border border-gray-300 shadow-sm px-2 py-2 bg-white 
                    text-sm font-medium text-gray-700 hover:bg-gray-50 
                    focus:outline-none"
                >
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="size-6">
  <path stroke-linecap="round" stroke-linejoin="round" d="M14.857 17.082a23.848 23.848 0 0 0 5.454-1.31A8.967 8.967 0 0 1 18 9.75V9A6 6 0 0 0 6 9v.75a8.967 8.967 0 0 1-2.312 6.022c1.733.64 3.56 1.085 5.455 1.31m5.714 0a24.255 24.255 0 0 1-5.714 0m5.714 0a3 3 0 1 1-5.714 0" />
</svg>

                   
                </button>
                
            </div>

            {isMenuOpen ? (
               <div
               className={`absolute -right-27 mt-2.5 flex h-90 w-75 flex-col rounded-sm border border-stroke bg-white shadow-default dark:border-strokedark dark:bg-boxdark sm:right-0 sm:w-80 p-2`}
             >
               <div className="px-4.5 py-3">
                 <h5 className="text-sm font-medium text-bodydark2">
                   Notification
                 </h5>
               </div>
   
               <ul className="flex h-auto flex-col overflow-y-auto">
                 <li>
                   <Link
                     className="flex flex-col gap-2.5 border-t border-stroke px-4.5 py-3 hover:bg-gray-2 dark:border-strokedark dark:hover:bg-meta-4"
                     to="#"
                   >
                     <p className="text-sm">
                       <span className="text-black dark:text-white">
                         Edit your information in a swipe
                       </span>{' '}
                       Sint occaecat cupidatat non proident, sunt in culpa qui
                       officia deserunt mollit anim.
                     </p>
   
                     <p className="text-xs">12 May, 2025</p>
                   </Link>
                 </li>
                 <li>
                   <Link
                     className="flex flex-col gap-2.5 border-t border-stroke px-4.5 py-3 hover:bg-gray-2 dark:border-strokedark dark:hover:bg-meta-4"
                     to="#"
                   >
                     <p className="text-sm">
                       <span className="text-black dark:text-white">
                         It is a long established fact
                       </span>{' '}
                       that a reader will be distracted by the readable.
                     </p>
   
                     <p className="text-xs">24 Feb, 2025</p>
                   </Link>
                 </li>
                 <li>
                   <Link
                     className="flex flex-col gap-2.5 border-t border-stroke px-4.5 py-3 hover:bg-gray-2 dark:border-strokedark dark:hover:bg-meta-4"
                     to="#"
                   >
                     <p className="text-sm">
                       <span className="text-black dark:text-white">
                         There are many variations
                       </span>{' '}
                       of passages of Lorem Ipsum available, but the majority have
                       suffered
                     </p>
   
                     <p className="text-xs">04 Jan, 2025</p>
                   </Link>
                 </li>
                 <li>
                   <Link
                     className="flex flex-col gap-2.5 border-t border-stroke px-4.5 py-3 hover:bg-gray-2 dark:border-strokedark dark:hover:bg-meta-4"
                     to="#"
                   >
                     <p className="text-sm">
                       <span className="text-black dark:text-white">
                         There are many variations
                       </span>{' '}
                       of passages of Lorem Ipsum available, but the majority have
                       suffered
                     </p>
   
                     <p className="text-xs">01 Dec, 2024</p>
                   </Link>
                 </li>
               </ul>
             </div>
            ) : null}
        </div>
    );
};

export default Notification;