import React, { useState } from "react";
import TextInput from "../components/TextInput";
import API from "../config/config";
import { useNavigate } from "react-router-dom";

const AddEmployee = () => {
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    fullName: "",
    emailId: "",
    phone: "",
    password: "",
    role: "",
    city: "",
  });

  const roles = {
    admin: "Admin",
    junior: "Junior",
    senior: "Senior",
  };
  const cities = ["Hyderabad"];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log(formData);
    try {
      const token = localStorage.getItem("adminToken");
      const response = await fetch(API.ADMIN.EMPLOYEES_ADD, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(formData),
      });
      if (response.ok) {
        navigate("/employees");
      } else {
        console.error("Failed to save employee");
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <div className="flex items-center justify-center -mt-12 px-4 bg-gray-100 min-h-screen">
      <form
        className="w-full bg-white shadow-lg rounded-lg p-4"
        onSubmit={handleSubmit}
      >
        <h2 className="text-2xl font-bold text-center mb-1">Add Employee</h2>
        <hr className="mb-3" />
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <TextInput
            name="fullName"
            label="Name"
            value={formData.fullName}
            onChange={handleChange}
            className="w-full"
            required={true}
          />

          <TextInput
            name="emailId"
            label="Email"
            value={formData.emailId}
            onChange={handleChange}
            className="w-full"
            required={true}
          />
          <TextInput
            name="phone"
            label="Phone"
            value={formData.phone}
            onChange={handleChange}
            className="w-full"
            required={true}
          />
          <TextInput
            name="password"
            label="Password"
            value={formData.password}
            onChange={handleChange}
            className="w-full"
            type="password"
            required={true}
          />
          <div className="w-full">
            <label
              htmlFor="role"
              className="block text-sm font-medium text-gray-700 mb-1"
            >
              City
            </label>
            <select
              name="city"
              value={formData.city}
              onChange={handleChange}
              className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
              required
            >
              <option value="">Select a city</option>
              {cities.map((city) => (
                <option key={city} value={city}>
                  {city}
                </option>
              ))}
            </select>
          </div>

          {/* Role Dropdown */}
          <div className="w-full">
            <label
              htmlFor="role"
              className="block text-sm font-medium text-gray-700 mb-1"
            >
              Role
            </label>
            <select
              name="role"
              value={formData.role}
              onChange={handleChange}
              className="w-full border border-gray-300 rounded-md px-3 py-2 focus:outline-none focus:ring-2 focus:ring-blue-400"
              required
            >
              <option value="">Select a role</option>
              {Object.entries(roles).map(([value, label]) => (
                <option key={value} value={value}>
                  {label}
                </option>
              ))}
            </select>
          </div>
        </div>

        <button
          type="submit"
          className="mt-4 w-1/4 bg-yellow-300 text-white font-semibold py-3 rounded-lg hover:bg-blue-700 transition-colors duration-300"
        >
          Submit
        </button>
      </form>
    </div>
  );
};

export default AddEmployee;
