import React, { useEffect, useState } from "react";
import {
  FaUserEdit,
  FaIdCard,
  FaAddressCard,
  FaCar,
  FaVideo,
  FaCheckCircle,
} from "react-icons/fa";
import { useParams } from "react-router-dom";
import API from "../../config/config";
import axiosInstance from "../../config/axiosInstance";

import StepProfilePhoto from "./StepProfilePhoto";
import StepDrivingLicense from "./StepDrivingLicense";
import StepAadhaar from "./StepAadhaar";
import StepPAN from "./StepPAN";
import StepChoices from "./StepChoices";
import StepVideos from "./StepVideos";
import StepBankDetails from "./StepBankDetails";

export default function StepperLayout() {
  const { id } = useParams();

  const [formData, setFormData] = useState({});
  const [currentStep, setCurrentStep] = useState(0);
  const [driver, setDriver] = useState(null);
  const [loading, setLoading] = useState(true);

  const fetchDriverDetails = async () => {
    try {
      const token = localStorage.getItem("adminToken");
      const { data } = await axiosInstance.get(
        `${API.ADMIN.DRIVER_DETAIL}/${id}`,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      const profile = data.data;
      console.log("Profile Data", profile);
      setDriver(profile);
      setFormFromProfile(profile);
    } catch (error) {
      console.error("Error fetching driver details:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDriverDetails();
  }, [id]);

  // Defensive fix added for profile.drivingChoices

  const setFormFromProfile = (profile) => {
    const toInputDateFormat = (dateStr) => {
      if (!dateStr) return "";
      if (dateStr.includes("-")) {
        const parts = dateStr.split("-");
        if (parts[2]?.length === 4) {
          return `${parts[2]}-${parts[1].padStart(2, "0")}-${parts[0].padStart(
            2,
            "0"
          )}`;
        }
      }
      return new Date(dateStr).toISOString().split("T")[0];
    };

    const drivingChoices = profile?.drivingChoices || {};
    const bankDetails = profile?.bankAccountDetails || {};
    const dlDetails = profile?.drivingLincenseDetails || {};
    const aadhaarDetails = profile?.aadhaarDetails || {};
    const panDetails = profile?.panDetails || {};
    const trainingVideos = profile?.trainingVideos || {};

    const initialForm = {
      profilePhoto: profile.profileImage || null,

      dlNumber: dlDetails.dlNumber || "",
      nameToMatch: dlDetails.nameToMatch || "",
      dateOfBirth: toInputDateFormat(dlDetails.dateOfBirth),
      dateOfIssue: toInputDateFormat(dlDetails.dateOfIssue),
      dateOfExpire: toInputDateFormat(dlDetails.dateOfExpire),
      dlfront: dlDetails.dlfront || null,
      dlback: dlDetails.dlback || null,

      aadhaarNumber: aadhaarDetails.aadhaarNumber || "",
      aadhaarFront: aadhaarDetails.aadhaarfront || null,
      aadhaarBack: aadhaarDetails.aadhaarback || null,
      aadhaarName: aadhaarDetails.nameToMatch || "",

      panNumber: panDetails.panNumber || "",
      holderName: panDetails.holderName || "",
      panFile: panDetails.panfront || null,

      tripType: drivingChoices.tripType || "",
      carTransmission: drivingChoices.carTransmission || "",
      carTypes: Array.isArray(drivingChoices.carTypes)
        ? drivingChoices.carTypes
        : drivingChoices.carTypes
        ? [drivingChoices.carTypes]
        : [],
      city: drivingChoices.city || "",
      area: drivingChoices.area || "",

      beneficiaryName: bankDetails.beneficiaryName || "",
      accountNumber: bankDetails.accountNumber || "",
      ifsc: bankDetails.ifsc || "",
      bankName: bankDetails.bankName || "",

      carType: trainingVideos.carType || "",
      transmission_video: trainingVideos.transmission_video || "",
      front_drive_video: trainingVideos.front_drive_video || "",
      reverse_parking_video: trainingVideos.reverse_parking_video || "",
    };

    setFormData(initialForm);

    const firstIncomplete = steps.findIndex(
      (step) => !step.isComplete(initialForm, profile)
    );
    setCurrentStep(firstIncomplete === -1 ? 0 : firstIncomplete);
  };

  const steps = [
    {
      key: "profilePhoto",
      label: "Profile Photo",
      icon: <FaUserEdit />,
      component: StepProfilePhoto,
      isComplete: (form, profile) => profile?.profileImage || form.profilePhoto,
    },
    {
      key: "dl",
      label: "DL Verification",
      icon: <FaIdCard />,
      component: StepDrivingLicense,
      isComplete: (form, profile) =>
        profile?.drivingLincenseDetails?.status === "Verified" ||
        (form.dlNumber &&
          form.nameToMatch &&
          form.dateOfBirth &&
          form.dateOfIssue &&
          form.dateOfExpire &&
          form.dlfront &&
          form.dlback),
    },
    {
      key: "aadhaar",
      label: "Aadhaar Verification",
      icon: <FaAddressCard />,
      component: StepAadhaar,
      isComplete: (form, profile) =>
        profile?.aadhaarDetails?.aadharVerified === "Verified" ||
        (form.aadhaarNumber && form.aadhaarFront && form.aadhaarBack),
    },
    {
      key: "pan",
      label: "PAN Verification",
      icon: <FaIdCard />,
      component: StepPAN,
      isComplete: (form, profile) =>
        profile?.panDetails?.panVerified === "Verified" ||
        (form.panNumber && form.panFile),
    },
    {
      key: "bank",
      label: "Bank Details",
      icon: <FaAddressCard />,
      component: StepBankDetails,
      isComplete: (form, profile) =>
        form.beneficiaryName &&
        form.accountNumber &&
        form.ifsc &&
        form.bankName,
    },
    {
      key: "choices",
      label: "Driving Choices",
      icon: <FaCar />,
      component: StepChoices,
      isComplete: (form, profile) =>
        form.tripType &&
        form.carTransmission &&
        form.carTypes?.length > 0 &&
        form.city &&
        form.area,
    },

    {
      key: "videos",
      label: "Driving Videos",
      icon: <FaVideo />,
      component: StepVideos,
      isComplete: (form, profile) =>
        profile?.trainingVideos === true ||
        (form.transmission_video &&
          form.front_drive_video &&
          form.reverse_parking_video &&
          form.carType),
    },
  ];

  const handleNext = () => {
    const step = steps[currentStep];
    if (step.isComplete(formData, driver)) {
      if (currentStep < steps.length - 1) {
        setCurrentStep((prev) => prev + 1);
      } else {
        alert(" All steps completed!");
      }
    } else {
      alert(" Please complete this step before proceeding.");
    }
  };

  const handleBack = () => {
    if (currentStep > 0) {
      setCurrentStep((prev) => prev - 1);
    }
  };

  if (loading) {
    return (
      <div className="h-screen flex items-center justify-center bg-white">
        <div className="animate-spin rounded-full h-12 w-12 border-t-4 border-b-4 border-indigo-600"></div>
      </div>
    );
  }

  const CurrentComponent = steps[currentStep].component;

  return (
    <div className="flex justify-center items-center min-h-screen bg-gradient-to-br from-blue-50 to-white px-4">
      <div className="w-full max-w-5xl bg-white rounded-2xl shadow-xl border border-blue-100 p-4 md:p-6">
        {/* Stepper */}
        <div className="flex flex-col md:flex-row items-center justify-between mb-10 space-y-4 md:space-y-0 md:space-x-4">
          {steps.map((step, index) => (
            <div
              key={step.key}
              className="flex flex-col items-center relative flex-1"
            >
              <div
                className={`w-12 h-12 rounded-full flex items-center justify-center border-2 font-bold text-lg z-10
                  ${
                    index === currentStep
                      ? "bg-blue-600 text-white border-blue-600"
                      : step.isComplete(formData, driver)
                      ? "bg-green-500 text-white border-green-500"
                      : "bg-gray-200 text-gray-600 border-gray-300"
                  }`}
              >
                {step.isComplete(formData, driver) ? (
                  <FaCheckCircle />
                ) : (
                  index + 1
                )}
              </div>
              <div className="text-center text-xs mt-2">{step.label}</div>
              {index !== steps.length - 1 && (
                <div className="hidden md:block absolute top-6 right-[-50%] w-full h-0.5 bg-gray-300 z-0" />
              )}
            </div>
          ))}
        </div>

        {/* Step Heading */}
        <div className="mb-8 text-center">
          <h2 className="text-2xl font-bold text-blue-700 mb-2">
            Step {currentStep + 1}: {steps[currentStep].label}
          </h2>
        </div>

        {/* Step Component */}
        <div className="mb-10">
          <CurrentComponent
            driver={driver}
            formData={formData}
            setFormData={setFormData}
            nextStep={handleNext}
            prevStep={handleBack}
            refreshDriver={fetchDriverDetails} //  THIS LINE
          />
        </div>

        {/* Navigation */}
        <div className="flex justify-between items-center">
          <div>
            <button
              onClick={handleBack}
              disabled={currentStep === 0}
              className="bg-gray-300 text-gray-800 px-6 py-2 rounded-lg hover:bg-gray-400 transition disabled:opacity-50"
            >
              Back
            </button>
          </div>

          <div>
            <button
              onClick={handleNext}
              className={`px-6 py-2 rounded-lg transition font-semibold ${
                steps[currentStep].isComplete(formData, driver)
                  ? "bg-blue-600 text-white hover:bg-blue-700"
                  : "bg-gray-400 text-white cursor-not-allowed"
              }`}
              disabled={!steps[currentStep].isComplete(formData, driver)}
            >
              {currentStep === steps.length - 1 ? "Submit" : "Next"}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
