import React, { useState, useEffect } from "react";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

const DriverStatusSelector = ({
  driverId,
  initialStatus,
  onStatusChange,
  driver,
}) => {
  const [status, setStatus] = useState(initialStatus || "");
  const [loading, setLoading] = useState(false);
  const [allowedStatuses, setAllowedStatuses] = useState([]);

  //  Only run once to fetch admin permissions
  useEffect(() => {
    const fetchPermissions = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        if (!token) return;

        const res = await axiosInstance.get(API.ADMIN.GET_ADMIN_PROFILE, {
          headers: { Authorization: `Bearer ${token}` },
        });

        const profile = res.data?.data;
        const canEditStatuses =
          profile?.driverProfilePermissions?.canEditStatuses || [];

        setAllowedStatuses(canEditStatuses);
      } catch (err) {
        console.error("Failed to fetch admin profile/permissions", err);
      }
    };

    fetchPermissions();
  }, []);

  const handleStatusUpdate = async (newStatus) => {
    setLoading(true);
    try {
      const token = localStorage.getItem("adminToken");

      const response = await axiosInstance.patch(
        `${API.ADMIN.DRIVER_UPDATE_STATUS}/${driverId}`,
        { status: newStatus },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      if (response.data.success) {
        setStatus(newStatus);
        if (onStatusChange) onStatusChange(newStatus);
      } else {
        alert("Failed to update status");
      }
    } catch (error) {
      console.error("Error updating status:", error);
      alert("Error updating status");
    } finally {
      setLoading(false);
    }
  };
  console.log("driver details in driver status selector", driver);
  //  Additional checks for document verification
  const isDocumentsVerified =
    driver?.aadhaarDetails?.status === "Verified" &&
    driver?.panDetails?.status === "Verified" &&
    driver?.drivingLincenseDetails?.status === "Verified";

  //  Don't show dropdown if either not verified or not permitted
  if (!isDocumentsVerified || !allowedStatuses.includes(status)) {
    return null;
  }

  return (
    <div className="mt-6 px-6 text-left">
      <label
        htmlFor="driver-status"
        className="block text-sm font-medium text-gray-700 mb-2"
      >
        Change Driver Status
      </label>
      <select
        id="driver-status"
        value={status}
        onChange={(e) => handleStatusUpdate(e.target.value)}
        disabled={loading}
        className="w-full text-sm px-4 py-2 rounded-lg border border-gray-300 shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white text-gray-800"
      >
        <option disabled>-- Select Status --</option>
        {allowedStatuses.map((s) => (
          <option key={s} value={s}>
            {s}
          </option>
        ))}
      </select>
    </div>
  );
};

export default DriverStatusSelector;
