import React, { useState } from "react";
import API from "../../config/config";

export default function StepDrivingLicense({
  formData,
  setFormData,
  driver,
  nextStep,
  prevStep,
  refreshDriver,
}) {
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem("adminToken");

  const today = new Date();
  const minDOB = new Date(
    today.getFullYear() - 21,
    today.getMonth(),
    today.getDate()
  );
  const maxDOI = new Date(
    today.getFullYear() - 2,
    today.getMonth(),
    today.getDate()
  );
  const minDOE = new Date(
    today.getFullYear() + 1,
    today.getMonth(),
    today.getDate()
  );

  const handleChange = (e) => {
    const { name, value } = e.target;
    // Validate Full Name - only letters and spaces allowed
    if (name === "nameToMatch") {
      const regex = /^[A-Za-z\s]*$/;
      if (!regex.test(value)) return; // reject invalid input
    }
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e, key) => {
    const file = e.target.files[0];
    if (file) {
      setFormData((prev) => ({ ...prev, [key]: file }));
    }
  };

  const uploadFile = async (file, type) => {
    const headers = new Headers();
    headers.append("Authorization", `Bearer ${token}`);

    const form = new FormData();
    form.append("documentFor", "Driver");
    form.append("type", type);
    form.append("ownerId", driver._id);
    form.append("file", file);

    const res = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
      method: "POST",
      headers,
      body: form,
    });

    if (!res.ok) throw new Error(`Upload failed for ${type}`);
    const data = await res.json();
    return data.document?.documentUrl;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const required = [
      "dlNumber",
      "nameToMatch",
      "dateOfBirth",
      "dateOfIssue",
      "dateOfExpire",
      "dlfront",
      "dlback",
    ];
    for (const key of required) {
      if (!formData[key]) {
        alert("Please fill all fields and upload both DL images.");
        return;
      }
    }

    const dob = new Date(formData.dateOfBirth);
    const doi = new Date(formData.dateOfIssue);
    const doe = new Date(formData.dateOfExpire);

    if (dob > minDOB) return alert("Age must be at least 21.");
    if (doi > maxDOI) return alert("DL must be issued at least 2 years ago.");
    if (doe < minDOE) return alert("DL must be valid for at least 1 year.");

    try {
      setLoading(true);

      const frontUrl =
        typeof formData.dlfront === "string"
          ? formData.dlfront
          : await uploadFile(formData.dlfront, "dlfront");

      const backUrl =
        typeof formData.dlback === "string"
          ? formData.dlback
          : await uploadFile(formData.dlback, "dlback");

      const payload = {
        dlNumber: formData.dlNumber,
        nameToMatch: formData.nameToMatch,
        dateOfBirth: formData.dateOfBirth,
        dateOfIssue: formData.dateOfIssue,
        dateOfExpire: formData.dateOfExpire,
        consent: "Y",
        consentText:
          "I hear by declare my consent agreement for fetching my information via ZOOP API",
        taskId: "f26eb21e-4c35-4491-b2d5-41fa0e545a34",
        state: "",
        dlfront: frontUrl,
        dlback: backUrl,
        ownerId: driver._id,
      };

      const res = await fetch(API.ADMIN.ADMIN_VERIFY_DRIVING_LICENSE, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      if (!res.ok) throw new Error("Verification failed");

      setFormData((prev) => ({
        ...prev,
        dlfront: frontUrl,
        dlback: backUrl,
      }));
      await refreshDriver();
      alert(" Driving License submitted successfully.");
      nextStep();
    } catch (err) {
      console.error("DL submit error:", err);
      alert(" Submission failed.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-4xl mx-auto p-4 bg-white rounded shadow text-sm"
    >
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* DL Number */}
        <div>
          <label className="block mb-1 font-medium text-sm">
            Driving License Number
          </label>
          <input
            type="text"
            name="dlNumber"
            value={formData.dlNumber || ""}
            onChange={handleChange}
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>

        {/* Full Name */}
        <div>
          <label className="block mb-1 font-medium text-sm">Full Name</label>
          <input
            type="text"
            name="nameToMatch"
            value={formData.nameToMatch || ""}
            onChange={handleChange}
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
      </div>

      {/* Dates Row */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-3">
        <div>
          <label className="block mb-1 font-medium text-sm">
            Date of Birth
          </label>
          <input
            type="date"
            name="dateOfBirth"
            value={formData.dateOfBirth || ""}
            max={minDOB.toISOString().split("T")[0]}
            onChange={handleChange}
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
        <div>
          <label className="block mb-1 font-medium text-sm">
            Date of Issue
          </label>
          <input
            type="date"
            name="dateOfIssue"
            value={formData.dateOfIssue || ""}
            max={maxDOI.toISOString().split("T")[0]}
            onChange={handleChange}
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
        <div>
          <label className="block mb-1 font-medium text-sm">
            Date of Expiry
          </label>
          <input
            type="date"
            name="dateOfExpire"
            value={formData.dateOfExpire || ""}
            min={minDOE.toISOString().split("T")[0]}
            onChange={handleChange}
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
      </div>

      {/* DL Uploads */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-3">
        <div>
          <label className="block mb-1 font-medium text-sm">
            Upload DL Front
          </label>
          <input
            type="file"
            accept="image/*,application/pdf"
            onChange={(e) => handleFileChange(e, "dlfront")}
            className="w-full border px-2 py-1 rounded text-sm"
            required={!formData.dlfront}
          />
          {formData.dlfront && (
            <img
              src={
                typeof formData.dlfront === "string"
                  ? formData.dlfront
                  : URL.createObjectURL(formData.dlfront)
              }
              alt="DL Back"
              className="w-24 mt-1 border rounded"
            />
          )}
        </div>
        <div>
          <label className="block mb-1 font-medium text-sm">
            Upload DL Back
          </label>
          <input
            type="file"
            accept="image/*"
            onChange={(e) => handleFileChange(e, "dlback")}
            className="w-full border px-2 py-1 rounded text-sm"
            required={!formData.dlback}
          />

          {formData.dlback && (
            <img
              src={
                typeof formData.dlback === "string"
                  ? formData.dlback
                  : URL.createObjectURL(formData.dlback)
              }
              alt="DL Back"
              className="w-24 mt-1 border rounded"
            />
          )}
        </div>
      </div>

      <div className="mt-4">
        <button
          type="submit"
          className={`w-full bg-indigo-600 text-white py-2 rounded text-sm ${
            loading ? "opacity-60 cursor-not-allowed" : ""
          }`}
          disabled={loading}
        >
          {loading ? "Submitting..." : "Submit & Continue"}
        </button>
      </div>
    </form>
  );
}
