import React, { useEffect, useState } from "react";
import API from "../../config/config";
import { toast } from "react-toastify";

export default function StepChoices({
  formData,
  setFormData,
  driver,
  nextStep,
  prevStep,
  refreshDriver,
}) {
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem("adminToken");

  // Prefill from driver if not already filled
  useEffect(() => {
    if (driver) {
      setFormData((prev) => ({
        ...prev,
        tripType: prev.tripType || driver.drivingChoices.tripType || "",
        carTransmission:
          prev.carTransmission || driver.drivingChoices.carTransmission || "",
        carTypes:
          prev.carTypes && prev.carTypes.length > 0
            ? prev.carTypes
            : driver.drivingChoices.carType
            ? [driver.drivingChoices.carType]
            : [],
        city: prev.city || driver.drivingChoices.city || "",
        area: prev.area || driver.drivingChoices.area || "",
      }));
    }
  }, [driver, setFormData]);

  const handleChange = (field, value) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
  };

  const toggleCarType = (type) => {
    let selected = formData.carTypes || [];

    const allOptions = ["Hatchback", "Sedan", "SUV", "Luxury", "All"];

    if (type === "All") {
      // If All selected, toggle everything
      const isAllSelected = selected.length === 5;
      selected = isAllSelected ? [] : [...allOptions];
    } else {
      if (selected.includes(type)) {
        selected = selected.filter((t) => t !== type && t !== "All");
      } else {
        selected = [...selected.filter((t) => t !== "All"), type];
      }

      // Auto-select All if all individual selected
      const coreTypes = ["Hatchback", "Sedan", "SUV", "Luxury"];
      if (coreTypes.every((t) => selected.includes(t))) {
        selected = [...coreTypes, "All"];
      }
    }

    setFormData((prev) => ({ ...prev, carTypes: selected }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const requiredFields = ["tripType", "carTransmission", "carTypes", "city"];
    for (const field of requiredFields) {
      if (
        !formData[field] ||
        (Array.isArray(formData[field]) && formData[field].length === 0)
      ) {
        toast.error(" Please complete all required fields.");
        return;
      }
    }

    try {
      setLoading(true);

      // Prepare car types for backend (remove "All", expand if needed)
      let finalCarTypes = [...formData.carTypes];
      if (finalCarTypes.includes("All")) {
        finalCarTypes = ["Hatchback", "Sedan", "SUV", "Luxury"];
      }

      const payload = {
        tripType: formData.tripType,
        carTransmission: formData.carTransmission,
        carType: finalCarTypes, //  Send full array
        city: formData.city,
        area: formData.area || "",
        ownerId: driver._id,
      };

      const res = await fetch(`${API.ADMIN.SAVE_ADMIN_DRIVING_CHOICES}`, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      if (!res.ok) throw new Error("Update failed");

      await refreshDriver();
      toast.success(" Driving choices saved.");
      nextStep();
    } catch (err) {
      console.error("Driving choices error:", err);
      toast.error(" Failed to save driving preferences.");
    } finally {
      setLoading(false);
    }
  };

  const carTypeOptions = ["Hatchback", "Sedan", "SUV", "Luxury", "All"];

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-4xl mx-auto p-4 bg-white rounded shadow text-sm"
    >
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Trip Type */}
        <div>
          <label className="block mb-1 font-medium text-sm">Trip Type</label>
          <div className="flex flex-wrap gap-3">
            {["in_city", "outstation", "both"].map((value) => (
              <label key={value} className="flex items-center gap-1 capitalize">
                <input
                  type="radio"
                  name="tripType"
                  value={value}
                  checked={formData.tripType === value}
                  onChange={() => handleChange("tripType", value)}
                />
                {value.replace("_", " ")}
              </label>
            ))}
          </div>
        </div>

        {/* Car Transmission */}
        <div>
          <label className="block mb-1 font-medium text-sm">
            Car Transmission
          </label>
          <div className="flex flex-wrap gap-3">
            {["manual", "automatic", "both"].map((value) => (
              <label key={value} className="flex items-center gap-1 capitalize">
                <input
                  type="radio"
                  name="carTransmission"
                  value={value}
                  checked={formData.carTransmission === value}
                  onChange={() => handleChange("carTransmission", value)}
                />
                {value}
              </label>
            ))}
          </div>
        </div>

        {/* Car Types */}
        <div>
          <label className="block mb-1 font-medium text-sm">Car Types</label>
          <div className="grid grid-cols-2 gap-2">
            {carTypeOptions.map((type) => (
              <label key={type} className="flex items-center gap-2 text-sm">
                <input
                  type="checkbox"
                  checked={(formData.carTypes || []).includes(type)}
                  onChange={() => toggleCarType(type)}
                />
                {type}
              </label>
            ))}
          </div>
        </div>

        {/* City */}
        <div>
          <label className="block mb-1 font-medium text-sm">
            City to Drive In
          </label>
          <input
            type="text"
            className="w-full border border-gray-300 rounded px-3 py-1 text-sm"
            value={formData.city || ""}
            onChange={(e) => handleChange("city", e.target.value)}
            required
          />
        </div>

        {/* Area */}
        <div>
          <label className="block mb-1 font-medium text-sm">
            Area (Optional)
          </label>
          <input
            type="text"
            className="w-full border border-gray-300 rounded px-3 py-1 text-sm"
            value={formData.area || ""}
            onChange={(e) => handleChange("area", e.target.value)}
          />
        </div>
      </div>

      <div className="mt-4">
        <button
          type="submit"
          className={`w-full bg-indigo-600 text-white py-2 rounded text-sm ${
            loading ? "opacity-60 cursor-not-allowed" : ""
          }`}
          disabled={loading}
        >
          {loading ? "Saving..." : "Submit & Continue"}
        </button>
      </div>
    </form>
  );
}
