import React, { useEffect, useState } from "react";
import API from "../../config/config";

const StepProfilePhoto = ({ driver, nextStep, formData, setFormData }) => {
  console.log(formData);
  const [file, setFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState(formData.profilePhoto || null);
  const [message, setMessage] = useState("");
  const [uploading, setUploading] = useState(false);
  const [uploaded, setUploaded] = useState(!!formData.profilePhoto);
  const [loading, setLoading] = useState(false);

  // Sync formData from driver if not already set
  useEffect(() => {
    if (!formData.profilePhoto && driver?.profilePhoto) {
      setFormData((prev) => ({
        ...prev,
        profilePhoto: driver.profilePhoto,
      }));
    }
  }, [driver, formData.profilePhoto, setFormData]);

  // Update preview when profilePhoto changes
  useEffect(() => {
    if (formData.profilePhoto) {
      const isAbsolute = /^https?:\/\//i.test(formData.profilePhoto);
      const fullUrl = isAbsolute
        ? formData.profilePhoto
        : `${API.BASE_URL}${formData.profilePhoto}`;
      setPreviewUrl(fullUrl);
      setUploaded(true);
    }
  }, [formData.profilePhoto]);

  const handleFileChange = (e) => {
    const selected = e.target.files[0];
    setFile(selected);
    setMessage("");
    setUploaded(false);

    if (selected) {
      const reader = new FileReader();
      reader.onloadend = () => setPreviewUrl(reader.result);
      reader.readAsDataURL(selected);
    } else {
      setPreviewUrl(null);
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setMessage("Please select a file first.");
      return;
    }

    setLoading(true);
    setUploading(true);
    setMessage("");

    try {
      const token = localStorage.getItem("adminToken");
      const formdata = new FormData();
      formdata.append("type", "profilepic");
      formdata.append("ownerId", driver._id);
      formdata.append("documentFor", "Driver");
      formdata.append("file", file);

      const response = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: formdata,
      });

      const result = await response.json();
      if (response.ok) {
        const uploadedUrl = result.document?.documentUrl;
        setFormData((prev) => ({
          ...prev,
          profilePhoto: uploadedUrl,
        }));
        setMessage(" Profile picture uploaded successfully.");
        setUploaded(true);
      } else {
        setMessage(result.message || " Upload failed.");
      }
    } catch (err) {
      console.error(err);
      setMessage(" An error occurred while uploading.");
    } finally {
      setUploading(false);
      setLoading(false);
    }
  };

  return (
    <div className="max-w-2xl mx-auto p-4 bg-white rounded shadow text-sm">
      <div>
        <label className="block mb-1 font-medium text-sm text-gray-700">
          Upload Profile Photo
        </label>
        <input
          type="file"
          name="profilePhoto"
          accept="image/*"
          onChange={handleFileChange}
          className="block w-full border px-2 py-1 rounded text-sm mb-3"
        />
      </div>

      {previewUrl && (
        <div className="mb-3 flex justify-center">
          <img
            src={previewUrl}
            alt="Preview"
            className="w-24 h-24 object-cover rounded-full border"
          />
        </div>
      )}

      {!uploaded ? (
        <button
          onClick={handleUpload}
          disabled={uploading}
          className="w-full bg-indigo-600 text-white py-2 rounded hover:bg-indigo-700 transition text-sm"
        >
          {uploading ? "Uploading..." : "Upload"}
        </button>
      ) : (
        <button
          type="button"
          onClick={nextStep}
          disabled={loading}
          className={`w-full bg-indigo-600 text-white py-2 rounded text-sm ${
            loading ? "opacity-60 cursor-not-allowed" : ""
          }`}
        >
          {loading ? "Submitting..." : "Submit & Continue"}
        </button>
      )}

      {message && (
        <p className="mt-2 text-center text-sm text-gray-600">{message}</p>
      )}
    </div>
  );
};

export default StepProfilePhoto;
