import React, { useEffect, useState } from "react";
import DataTable from "../components/DataTable";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

const Issues = () => {
  const [issues, setIssues] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchIssues = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(API.ADMIN.ISSUES_LIST, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setIssues(response.data.data);
      } catch (error) {
        console.error("Error fetching issues:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchIssues();
  }, []);

  const handleDelete = async (issueId) => {
    try {
      await axiosInstance.delete(`${API.ADMIN.ISSUE_DELETE}/${issueId}`);
      setIssues(issues.filter((issue) => issue._id !== issueId));
    } catch (error) {
      console.error("Error deleting issue:", error);
    }
  };

  const columns = [
    { Header: "Issue ID", accessor: "_id" },
    { Header: "Title", accessor: "title" },
    { Header: "Message", accessor: "message" },
    { Header: "Issue Type", accessor: "issueType" },
    { Header: "Additional Message", accessor: "additionalMessage" },
    { Header: "Phone", accessor: "phone" },
    { Header: "Role", accessor: "role" },
    {
      Header: "Created At",
      accessor: "createdAt",
      Cell: ({ value }) => new Date(value).toLocaleString(),
    },
  ];

  return (
    <div className="px-4 py-6">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Issues</h1>
      </div>
      <DataTable
        data={issues}
        columns={columns}
        loading={loading}
        deleteHandler={handleDelete}
        addLink={null}
        editPath={null}
        detailPath={null}
      />
    </div>
  );
};

export default Issues;
