import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

import PersonalInformation from "../components/PersonalInformation";
import LanguagePreferences from "../components/LanguagePreferences";
import AadhaarDetails from "../components/AadhaarDetails";
import PANDetails from "../components/PANDetails";
import BankDetails from "../components/BankDetails";
import DrivingLicenseDetails from "../components/DrivingLicenseDetails";
import DrivingChoices from "../components/DrivingChoices";
import TrainingVideos from "../components/TrainingVideos";
import DriverStatusSelector from "../components/DriverStatusSelector";
import { FaUserCircle } from "react-icons/fa";

const DriverDetails = () => {
  const [driver, setDriver] = useState(null);
  const [loading, setLoading] = useState(true);
  const [status, setStatus] = useState("");
  const [currentUserRole, setCurrentUserRole] = useState(""); //  Store role
  const { id } = useParams();

  const canEditSection = () => {
    if (currentUserRole === "superadmin") return true;

    if (currentUserRole === "junior") {
      // Junior can edit only when status is Pending or Revisit
      return ["Pending", "Revisit"].includes(driver.status);
    }

    if (currentUserRole === "senior") {
      // Senior can edit until driver is onboarded
      return driver.status !== "Onboarded";
    }

    return false;
  };

  const fetchDriverDetails = async () => {
    try {
      const token = localStorage.getItem("adminToken");

      const [driverRes, profileRes] = await Promise.all([
        axiosInstance.get(`${API.ADMIN.DRIVER_DETAIL}/${id}`, {
          headers: { Authorization: `Bearer ${token}` },
        }),
        axiosInstance.get(API.ADMIN.GET_ADMIN_PROFILE, {
          headers: { Authorization: `Bearer ${token}` },
        }),
      ]);

      setDriver(driverRes.data.data);
      setStatus(driverRes.data.data.status);
      setCurrentUserRole(profileRes.data?.data?.role?.slug || "");
    } catch (error) {
      console.error("Error fetching driver or profile details:", error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchDriverDetails();
  }, [id]);

  if (loading) return <div className="text-center py-20">Loading...</div>;
  if (!driver)
    return <div className="text-center py-20">Driver not found.</div>;

  return (
    <div className="min-h-screen bg-gray-100 py-10 px-6">
      <div className="max-w-5xl mx-auto bg-white rounded-lg shadow-lg overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-500 to-purple-600 text-white p-6 text-center">
          <h1 className="text-3xl font-semibold">Driver Details</h1>
          <p className="text-lg">{driver.fullName}</p>
          {driver.profileImage ? (
            <img
              src={driver.profileImage}
              alt="Driver Profile"
              className="mx-auto mt-4 w-32 h-32 rounded-full border-4 border-white shadow-md"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = ""; // Hide broken image
              }}
            />
          ) : (
            <FaUserCircle className="mx-auto mt-4 text-white w-32 h-32 rounded-full border-4 border-white shadow-md bg-gray-300" />
          )}
        </div>

        {/* Status Selector */}
        <DriverStatusSelector
          driverId={driver._id}
          initialStatus={status}
          onStatusChange={(updatedStatus) => setStatus(updatedStatus)}
          driver={driver}
        />

        {/* Info Sections */}
        <PersonalInformation
          driver={driver}
          editable={canEditSection()}
          onUpdated={fetchDriverDetails}
        />
        <LanguagePreferences languagePreference={driver.languagePreference} />
        <DrivingLicenseDetails
          drivingLicense={driver.drivingLincenseDetails}
          driverId={driver._id}
          editable={canEditSection()}
        />
        <AadhaarDetails
          aadhaar={driver.aadhaarDetails}
          driverId={driver._id}
          editable={canEditSection()}
        />
        <PANDetails
          pan={driver.panDetails}
          driverId={driver._id}
          editable={canEditSection()}
        />
        <BankDetails
          bank={driver.bankAccountDetails}
          editable={
            currentUserRole === "junior" &&
            ["Pending", "Revisit"].includes(driver.status)
          }
        />

        <DrivingChoices drivingChoices={driver.drivingChoices} />

        {driver.trainingVideos?.length > 0 && (
          <TrainingVideos
            videos={driver.trainingVideos}
            verified={driver.trainingVerified}
          />
        )}
      </div>
    </div>
  );
};

export default DriverDetails;
