import React, { useState, useEffect } from "react";
import axios from "axios";
import API from "../config/config";

const RateCard = () => {
  const [masterRateCard, setMasterRateCard] = useState({});
  const [cityRateCards, setCityRateCards] = useState([]);
  const [editingField, setEditingField] = useState(null);
  const [newCity, setNewCity] = useState("");
  const [newCityRates, setNewCityRates] = useState({
    perMinute: 0,
    perHour: 0,
    minChargeInCity: 0,
    perKmInCity: 0,
    minChargeOutstation: 0,
    perKmOutstation: 0,
  });

  useEffect(() => {
    fetchRateCards();
  }, []);

  const fetchRateCards = async () => {
    try {
      const { data } = await axios.get(API.ADMIN.MASTER_RATE_CARD);
      setMasterRateCard(data.masterRateCard || {});
      setCityRateCards(data.cityRateCards || []);
    } catch (error) {
      console.error("Error fetching rate cards", error);
    }
  };

  const handleSaveCity = async (city, field, value) => {
    try {
      const updatedCityCards = cityRateCards.map((card) =>
        card.city === city
          ? { ...card, rates: { ...card.rates, [field]: value } }
          : card
      );
      const updatedCity = updatedCityCards.find((card) => card.city === city);
      await axios.put(
        `${API.ADMIN.UPDATE_CITY_RATE_CARD}/${city}`,
        updatedCity.rates
      );
      setCityRateCards(updatedCityCards);
      setEditingField(null);
    } catch (error) {
      console.error("Error updating city rate card", error);
    }
  };

  const handleAddCity = async () => {
    if (!newCity.trim()) return alert("City name is required");

    try {
      const newCityRateCard = {
        city: newCity.trim(),
        rates: newCityRates,
      };
      const { data } = await axios.put(
        API.ADMIN.ADD_CITY_RATE_CARD,
        newCityRateCard
      );
      setCityRateCards([...cityRateCards, data]);
      setNewCity("");
      setNewCityRates({
        perMinute: 0,
        perHour: 0,
        minChargeInCity: 0,
        perKmInCity: 0,
        minChargeOutstation: 0,
        perKmOutstation: 0,
      });
    } catch (error) {
      console.error("Error adding new city rate card", error);
    }
  };

  const handleSaveMasterRateCard = async (field, value) => {
    try {
      const updatedMasterRateCard = { ...masterRateCard, [field]: value };

      await axios.put(API.ADMIN.UPDATE_MASTER_RATE_CARD, updatedMasterRateCard);

      setMasterRateCard(updatedMasterRateCard);
      setEditingField(null);
    } catch (error) {
      console.error("Error updating master rate card", error);
    }
  };

  const renderEditableCell = (value, saveFn, field) => {
    if (editingField === field) {
      return (
        <input
          type="number"
          defaultValue={value}
          onBlur={(e) => saveFn(field, Number(e.target.value))}
          autoFocus
          className="border p-1 w-full"
        />
      );
    }
    return (
      <span onClick={() => setEditingField(field)} className="cursor-pointer">
        {value}
      </span>
    );
  };

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Rate Card Management</h1>

      {/* Master Rate Card */}
      <h2 className="text-xl font-semibold mb-2">Master Rate Card</h2>
      {Object.keys(masterRateCard).length > 0 ? (
        <table className="w-full border-collapse border border-gray-300 mt-2">
          <thead>
            <tr className="bg-gray-200">
              <th className="border border-gray-300 px-4 py-2">Field</th>
              <th className="border border-gray-300 px-4 py-2">Value</th>
            </tr>
          </thead>
          <tbody>
            {Object.keys(masterRateCard || {}).map((key) => (
              <tr key={key}>
                <td className="border border-gray-300 px-4 py-2">{key}</td>
                <td className="border border-gray-300 px-4 py-2">
                  {renderEditableCell(
                    masterRateCard[key],
                    (field, value) => handleSaveMasterRateCard(field, value),
                    key
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <p className="text-center text-gray-500">
          No master rate card available.
        </p>
      )}

      {/* City-Specific Rate Cards */}
      <h2 className="text-xl font-semibold mb-2">City-Specific Rates</h2>
      {cityRateCards.length > 0 ? (
        cityRateCards.map((card) => (
          <div key={card.city} className="mb-6">
            <h3 className="text-lg font-semibold bg-gray-200 p-2 rounded">
              {card.city}
            </h3>
            <table className="w-full border-collapse border border-gray-300 mt-2">
              <thead>
                <tr className="bg-gray-200">
                  <th className="border border-gray-300 px-4 py-2">Field</th>
                  <th className="border border-gray-300 px-4 py-2">Value</th>
                </tr>
              </thead>
              <tbody>
                {Object.keys(card.rates || {}).map((key) => (
                  <tr key={`${card.city}-${key}`}>
                    <td className="border border-gray-300 px-4 py-2">{key}</td>
                    <td className="border border-gray-300 px-4 py-2">
                      {renderEditableCell(
                        card.rates[key],
                        (field, value) =>
                          handleSaveCity(card.city, field, value),
                        `${card.city}-${key}`
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        ))
      ) : (
        <p className="text-center text-gray-500">
          No city-specific rates available.
        </p>
      )}

      {/* Add New City */}
      <h2 className="text-xl font-semibold mb-2">Add City</h2>
      <div className="mb-4 flex items-center gap-2">
        <input
          type="text"
          placeholder="City Name"
          value={newCity}
          onChange={(e) => setNewCity(e.target.value)}
          className="border px-4 py-2 flex-1"
        />
        <button
          onClick={handleAddCity}
          className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600"
        >
          Add City
        </button>
      </div>

      <table className="w-full border-collapse border border-gray-300">
        <thead>
          <tr className="bg-gray-200">
            <th className="border border-gray-300 px-4 py-2">Field</th>
            <th className="border border-gray-300 px-4 py-2">Value</th>
          </tr>
        </thead>
        <tbody>
          {Object.keys(newCityRates).map((key) => (
            <tr key={key}>
              <td className="border border-gray-300 px-4 py-2">{key}</td>
              <td className="border border-gray-300 px-4 py-2">
                <input
                  type="number"
                  value={newCityRates[key]}
                  onChange={(e) =>
                    setNewCityRates({
                      ...newCityRates,
                      [key]: Number(e.target.value),
                    })
                  }
                  className="w-full px-2 py-1 border"
                />
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default RateCard;
