import React from "react";
import { useNavigate } from "react-router-dom";

const statusColors = {
  Pending: "bg-yellow-100 text-yellow-800 border-yellow-300",
  Revisit: "bg-orange-100 text-orange-800 border-orange-300",
  Approved: "bg-lime-100 text-lime-800 border-lime-300",
  Rejected: "bg-red-100 text-red-800 border-red-300",
  Onboarded: "bg-green-100 text-green-800 border-green-300",
};

const DriverStatusSummaryCards = ({ summaryData }) => {
  const navigate = useNavigate();

  const statuses = [
    { label: "Pending", key: "Pending" },
    { label: "In Revisit", key: "Revisit" },
    { label: "Approved", key: "Approved" },
    { label: "Rejected", key: "Rejected" },
    { label: "Onboarded", key: "Onboarded" },
  ];

  const handleClick = (status) => {
    // Navigate to driver list with filter applied
    navigate(`/drivers?status=${status}`);
  };

  return (
    <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
      {statuses.map((status) => {
        const count = summaryData?.[status.key] || 0;
        return (
          <div
            key={status.key}
            onClick={() => handleClick(status.key)}
            className={`cursor-pointer border rounded-2xl shadow-sm p-4 transition hover:shadow-md ${
              statusColors[status.key]
            }`}
          >
            <h3 className="text-lg font-semibold">{status.label}</h3>
            <p className="text-3xl font-bold mt-2">{count}</p>
          </div>
        );
      })}
    </div>
  );
};

export default DriverStatusSummaryCards;
