import React from "react";

const DrivingChoices = ({ drivingChoices }) => {
  const noChoices =
    !drivingChoices ||
    (!drivingChoices.tripType &&
      !drivingChoices.carTransmission &&
      !drivingChoices.carType &&
      !drivingChoices.city &&
      !drivingChoices.area);

  if (noChoices) return null;

  return (
    <section className="p-6">
      <h2 className="text-2xl font-semibold text-gray-800 mb-4">
        Driving Choices
      </h2>
      <p>
        <strong>Trip Type:</strong> {drivingChoices.tripType || "Not Set"}
      </p>
      <p>
        <strong>Car Transmission:</strong>{" "}
        {drivingChoices.carTransmission || "Not Set"}
      </p>
      <p>
        <strong>Car Type:</strong> {drivingChoices.carType || "Not Set"}
      </p>
      <p>
        <strong>City:</strong> {drivingChoices.city || "Not Set"}
      </p>
      <p>
        <strong>Area:</strong> {drivingChoices.area || "Not Set"}
      </p>
    </section>
  );
};

export default DrivingChoices;
