import React, { useState, useEffect } from "react";
import API from "../../config/config";
import { toast } from "react-toastify";

export default function StepBankDetails({
  driver,
  formData,
  setFormData,
  nextStep,
  prevStep,
  refreshDriver,
}) {
  console.log(driver);
  const [loading, setLoading] = useState(false);
  const token = localStorage.getItem("adminToken");

  // Optional: Sync formData from parent if driver changes (usually not required if parent updates formData correctly)
  useEffect(() => {
    if (driver) {
      setFormData((prev) => ({
        ...prev,
        beneficiaryName:
          driver.bankAccountDetails?.beneficiaryName ||
          prev.beneficiaryName ||
          "",
        accountNumber:
          driver.bankAccountDetails?.accountNumber || prev.accountNumber || "",
        ifsc: driver.bankAccountDetails?.ifsc || prev.ifsc || "",
        bankName: driver.bankAccountDetails?.bankName || prev.bankName || "",
      }));
    }
  }, [driver, setFormData]);

  // Handle input changes - directly update formData prop in parent
  const handleChange = (e) => {
    const { name, value } = e.target;

    let cleanedValue = value;

    if (name === "beneficiaryName" || name === "bankName") {
      // Allow only alphabets and spaces
      cleanedValue = value.replace(/[^A-Za-z\s]/g, "");
    }

    if (name === "accountNumber") {
      // Allow only digits
      cleanedValue = value.replace(/\D/g, "");
    }

    if (name === "ifsc") {
      // Allow uppercase alphanumeric only, and auto-uppercase
      cleanedValue = value.toUpperCase().replace(/[^A-Z0-9]/g, "");
    }
    setFormData((prev) => ({ ...prev, [name]: cleanedValue }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const { beneficiaryName, accountNumber, ifsc, bankName } = formData;

    if (!beneficiaryName || !accountNumber || !ifsc || !bankName) {
      toast.error(" Please fill in all required fields.");
      return;
    }

    setLoading(true);

    try {
      const payload = {
        ownerId: driver._id,
        beneficiaryName,
        accountNumber,
        ifsc,
        bankName,
        consent: "Y",
        consentText: "I give my consent to verify my bank account.",
        taskId: null,
      };

      const response = await fetch(API.ADMIN.ADMIN_VERIFY_BANK, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payload),
      });

      const result = await response.json();

      if (response.ok && result.success) {
        toast.success(" Bank details verified successfully.");

        // Update formData with confirmed values
        setFormData((prev) => ({
          ...prev,
          beneficiaryName,
          accountNumber,
          ifsc,
          bankName,
        }));

        await refreshDriver(); // Refresh parent driver data
        nextStep(); // Move to next step
      } else {
        toast.error(
          result?.response?.response_message ||
            result?.message ||
            " Bank verification failed."
        );
      }
    } catch (error) {
      console.error("Bank verification error:", error);
      toast.error("Something went wrong. Please try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-4xl mx-auto p-4 bg-white rounded shadow text-sm"
    >
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div>
          <label className="block mb-1 font-medium text-gray-700 text-sm">
            Account Holder Name
          </label>
          <input
            type="text"
            name="beneficiaryName"
            value={formData.beneficiaryName || ""}
            onChange={handleChange}
            placeholder="Enter account holder name"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>

        <div>
          <label className="block mb-1 font-medium text-gray-700 text-sm">
            Account Number
          </label>
          <input
            type="text"
            name="accountNumber"
            value={formData.accountNumber || ""}
            onChange={handleChange}
            placeholder="Enter account number"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>

        <div>
          <label className="block mb-1 font-medium text-gray-700 text-sm">
            IFSC Code
          </label>
          <input
            type="text"
            name="ifsc"
            value={formData.ifsc || ""}
            onChange={handleChange}
            placeholder="Enter IFSC code"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>

        <div>
          <label className="block mb-1 font-medium text-gray-700 text-sm">
            Bank Name
          </label>
          <input
            type="text"
            name="bankName"
            value={formData.bankName || ""}
            onChange={handleChange}
            placeholder="Enter bank name"
            className="w-full border px-2 py-1 rounded text-sm"
            required
          />
        </div>
      </div>

      <div className="mt-6 flex justify-between">
        <button
          type="submit"
          className={`px-6 py-2 rounded text-white font-semibold transition text-sm ${
            loading
              ? "bg-indigo-400 cursor-not-allowed"
              : "bg-indigo-600 hover:bg-indigo-700"
          }`}
          disabled={loading}
        >
          {loading ? "Verifying..." : "Submit & Continue"}
        </button>
      </div>
    </form>
  );
}
