import React, { useState, useRef } from "react";
import API from "../../config/config";
import { toast } from "react-toastify";

export default function StepVideos({
  formData,
  setFormData,
  driver,
  nextStep,
  prevStep,
  refreshDriver,
}) {
  const [uploadingField, setUploadingField] = useState(null);
  const token = localStorage.getItem("adminToken");

  // Holds the latest uploaded URLs safely (avoids async state issues)
  const videoURLs = useRef({
    transmission_video: formData.transmission_video || "",
    front_drive_video: formData.front_drive_video || "",
    reverse_parking_video: formData.reverse_parking_video || "",
  });

  const uploadVideoFile = async (file, type) => {
    const formDataUpload = new FormData();
    formDataUpload.append("documentFor", "Driver");

    if (type === "transmission_video") {
      formDataUpload.append("type", "transmission");
    } else if (type === "front_drive_video") {
      formDataUpload.append("type", "front_drive");
    } else if (type === "reverse_parking_video") {
      formDataUpload.append("type", "reverse_parking");
    }

    formDataUpload.append("ownerId", driver._id);
    formDataUpload.append("video", file, file.name);
    formDataUpload.append("uploadTo", "s3");

    const response = await fetch(API.ADMIN.VIDEO_ADMIN_UPLOAD, {
      method: "POST",
      headers: {
        Authorization: "Bearer " + token,
      },
      body: formDataUpload,
    });

    const data = await response.json();
    console.log("Upload response:", data);

    if (!response.ok || !data?.data?.videoUrl) {
      throw new Error(data?.message || "Video upload failed");
    }

    return data.data.videoUrl;
  };

  const handleFileChange = async (field, file) => {
    if (!file) return;
    try {
      setUploadingField(field);
      const uploadedUrl = await uploadVideoFile(file, field);

      // Update ref immediately
      videoURLs.current[field] = uploadedUrl;

      // Update formData
      setFormData((prev) => ({
        ...prev,
        [field]: uploadedUrl,
      }));

      toast.success(`${field.replace(/_/g, " ")} uploaded successfully`);
    } catch (err) {
      console.error(err);
      toast.error(err.message || `Upload failed for ${field}`);
    } finally {
      setUploadingField(null);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { transmission_video, front_drive_video, reverse_parking_video } =
      videoURLs.current;
    const carType = formData.carType;

    console.log(" Submitting with values:", {
      transmission_video,
      front_drive_video,
      reverse_parking_video,
      carType,
    });

    if (
      !transmission_video ||
      !front_drive_video ||
      !reverse_parking_video ||
      !carType
    ) {
      toast.error("Please upload all videos and select car type.");
      return;
    }

    try {
      const response = await fetch(API.ADMIN.ADMIN_SAVE_DRIVING_VIDEOS, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + token,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          ownerId: driver._id,
          transmission_video,
          front_drive_video,
          reverse_parking_video,
          carType,
        }),
      });

      const resData = await response.json();

      if (!response.ok) {
        throw new Error(resData.message || "Failed to save video details");
      }

      await refreshDriver();
      toast.success(" Driving videos saved successfully!");
      nextStep();
    } catch (err) {
      console.error(err);
      toast.error(err.message || "Submit failed");
    }
  };

  const renderVideoPreview = (videoUrl) => {
    return videoUrl ? (
      <video
        controls
        src={videoUrl}
        className="w-full mt-1 border rounded max-h-48"
      />
    ) : null;
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-4xl mx-auto p-4 bg-white rounded shadow text-sm space-y-6"
    >
      {/* Car Type */}
      <div>
        <label className="block mb-1 font-medium text-gray-700">
          Your Driving Car Type
        </label>
        <div className="flex flex-wrap gap-4 text-sm">
          {["Hatchback", "Sedan", "SUV", "Luxury", "All"].map((type) => (
            <label key={type} className="flex items-center gap-1">
              <input
                type="radio"
                name="carType"
                value={type}
                checked={formData.carType === type}
                onChange={(e) =>
                  setFormData((prev) => ({
                    ...prev,
                    carType: e.target.value,
                  }))
                }
              />
              {type}
            </label>
          ))}
        </div>
      </div>

      {/* Video Uploads */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {[
          { field: "transmission_video", label: "Transmission Video" },
          { field: "front_drive_video", label: "Front Drive Video" },
          { field: "reverse_parking_video", label: "Reverse Parking Video" },
        ].map(({ field, label }) => (
          <div key={field}>
            <label className="block mb-1 font-medium text-gray-700">
              {label} <span className="text-red-500">*</span>
            </label>
            <input
              type="file"
              accept="video/*"
              onChange={(e) => handleFileChange(field, e.target.files[0])}
              className="w-full border p-2 rounded"
              disabled={uploadingField === field}
              required={!formData[field]}
            />
            {uploadingField === field && (
              <p className="text-sm text-indigo-600 mt-1">Uploading...</p>
            )}
            {renderVideoPreview(formData[field])}
          </div>
        ))}
      </div>

      {/* Submit */}
      <div className="flex justify-end mt-6">
        <button
          type="submit"
          disabled={
            uploadingField !== null ||
            !videoURLs.current.transmission_video ||
            !videoURLs.current.front_drive_video ||
            !videoURLs.current.reverse_parking_video ||
            !formData.carType
          }
          className={`bg-indigo-600 text-white px-6 py-2 rounded ${
            uploadingField
              ? "opacity-60 cursor-not-allowed"
              : "hover:bg-indigo-700"
          }`}
        >
          {uploadingField ? "Uploading..." : "Submit & Continue"}
        </button>
      </div>
    </form>
  );
}
