import React, { useState } from "react";
import {
  FaCar,
  FaEdit,
  FaCheck,
  FaTimes,
  FaUpload,
  FaSpinner,
} from "react-icons/fa";
import API from "../config/config";
import axios from "axios";

const DrivingLicenseDetails = ({ drivingLicense, driverId, editable }) => {
  const token = localStorage.getItem("adminToken");

  //  Hooks: MUST come first, always
  const [editField, setEditField] = useState("");
  const [uploadingSide, setUploadingSide] = useState(null); // 'dlfront' or 'dlback'
  const [isSaving, setIsSaving] = useState(false);
  const [formData, setFormData] = useState({
    dateOfBirth: drivingLicense?.dateOfBirth || "",
    dateOfIssue: drivingLicense?.dateOfIssue || "",
    dateOfExpire: drivingLicense?.dateOfExpire || "",
    dlNumber: drivingLicense?.dlNumber || "",
    status: drivingLicense?.status,
    rejectionReason: drivingLicense?.rejectionReason || "",
    dlfront: drivingLicense?.dlfront,
    dlback: drivingLicense?.dlback,
  });

  //  Now safe to early-return
  const noDLData =
    !drivingLicense ||
    (!drivingLicense.dlNumber &&
      !drivingLicense.dlfront &&
      !drivingLicense.dlback &&
      !drivingLicense.dateOfBirth &&
      !drivingLicense.dateOfIssue &&
      !drivingLicense.dateOfExpire);

  if (noDLData) return null;

  const formatDisplayDate = (isoDateStr) => {
    if (!isoDateStr) return "N/A";
    const date = new Date(isoDateStr);
    return date.toLocaleDateString("en-IN"); // format: DD/MM/YYYY
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFileChange = async (e, side) => {
    const file = e.target.files[0];
    if (!file) return;

    setUploadingSide(side);

    try {
      const formdata = new FormData();
      formdata.append("documentFor", "Driver");
      formdata.append("type", side);
      formdata.append("file", file, file.name);
      formdata.append("ownerId", driverId);

      const response = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + token,
        },
        body: formdata,
      });

      const data = await response.json();
      const uploadedUrl = data.document?.documentUrl;
      if (!uploadedUrl) throw new Error("Upload failed");

      await axios.patch(
        `${API.ADMIN.DRIVER_LICENSEDETAILS}/${drivingLicense._id}`,
        { [side]: uploadedUrl },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );

      setFormData((prev) => ({
        ...prev,
        [side]: uploadedUrl,
      }));
    } catch (err) {
      console.error("Upload error:", err);
      alert("Failed to upload DL image.");
    } finally {
      setUploadingSide(null);
    }
  };

  const handleSaveField = async (field) => {
    setIsSaving(true);
    try {
      const payload = {};
      if (field === "status") {
        payload.status = formData.status;
        if (formData.status === "Rejected") {
          payload.rejectionReason = formData.rejectionReason;
        }
      } else {
        payload[field] = formData[field];
      }

      await axios.patch(
        `${API.ADMIN.DRIVER_LICENSEDETAILS}/${drivingLicense._id}`,
        payload,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      setEditField("");
    } catch (err) {
      console.error("Update error:", err);
      alert("Failed to update field");
    } finally {
      setIsSaving(false);
    }
  };

  const renderEditableTextField = (label, fieldName) => (
    <div>
      <strong>{label}:</strong>{" "}
      {editField === fieldName ? (
        <span className="flex gap-2 items-center">
          <input
            type={
              ["dateOfBirth", "dateOfIssue", "dateOfExpire"].includes(fieldName)
                ? "date"
                : "text" //  Use text for dlNumber
            }
            name={fieldName}
            value={formData[fieldName]}
            onChange={handleChange}
            className="border px-2 py-1 rounded text-sm"
          />
          {isSaving ? (
            <FaSpinner className="animate-spin text-gray-500" />
          ) : (
            <>
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={() => handleSaveField(fieldName)}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setFormData((p) => ({
                    ...p,
                    [fieldName]: drivingLicense[fieldName] || "",
                  }));
                  setEditField("");
                }}
              />
            </>
          )}
        </span>
      ) : (
        <span className="flex items-center gap-2">
          {["dateOfBirth", "dateOfIssue", "dateOfExpire"].includes(fieldName)
            ? formatDisplayDate(formData[fieldName])
            : formData[fieldName] || "N/A"}
          {editable && (
            <FaEdit
              className="text-blue-600 cursor-pointer"
              onClick={() => setEditField(fieldName)}
            />
          )}
        </span>
      )}
    </div>
  );

  return (
    <section className="p-6 bg-white rounded shadow mb-6">
      <div className="flex items-center gap-4 mb-6">
        <FaCar className="text-blue-600 text-2xl" />
        <h2 className="text-2xl font-semibold text-gray-800">
          Driving License Details
        </h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {renderEditableTextField("Driving License Number", "dlNumber")}
        {renderEditableTextField("Date of Birth", "dateOfBirth")}
        {renderEditableTextField("Date of Issue", "dateOfIssue")}
        {renderEditableTextField("Date of Expiry", "dateOfExpire")}

        {/* DL Images */}
        {(formData.dlfront || formData.dlback) && (
          <div className="md:col-span-2">
            <strong className="block mb-2">DL Images:</strong>
            <div className="flex gap-8 flex-wrap md:flex-nowrap">
              {["dlfront", "dlback"].map((side) => (
                <div key={side} className="flex flex-col items-start">
                  <span className="font-medium mb-1 capitalize">
                    {side === "dlfront" ? "Front" : "Back"}
                  </span>
                  <img
                    src={formData[side]}
                    alt={side}
                    className="w-72 h-44 object-cover border rounded shadow"
                  />
                  {editable && (
                    <label className="mt-2 cursor-pointer flex gap-2 items-center text-sm text-blue-600">
                      {uploadingSide === side ? (
                        <FaSpinner className="animate-spin" />
                      ) : (
                        <>
                          <FaUpload />
                          Change
                        </>
                      )}
                      <input
                        type="file"
                        accept="image/*"
                        className="hidden"
                        onChange={(e) => handleFileChange(e, side)}
                        disabled={uploadingSide}
                      />
                    </label>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/* Status Section */}
      <div className="mt-6 border-t pt-4">
        <strong>Status:</strong>{" "}
        {editField === "status" ? (
          <div className="grid gap-4 mt-2">
            <select
              name="status"
              value={formData.status}
              onChange={handleChange}
              className="border p-2 rounded w-full"
            >
              <option value="InProgress">In Progress</option>
              <option value="Verified">Verified</option>
              <option value="Rejected">Rejected</option>
            </select>

            {formData.status === "Rejected" && (
              <textarea
                name="rejectionReason"
                value={formData.rejectionReason}
                onChange={handleChange}
                placeholder="Rejection Reason"
                className="border p-2 rounded w-full"
              />
            )}

            <div className="flex justify-between">
              <button
                type="button"
                onClick={() => {
                  setEditField("");
                  setFormData((p) => ({
                    ...p,
                    status: drivingLicense.status,
                    rejectionReason: drivingLicense.rejectionReason || "",
                  }));
                }}
                className="bg-gray-600 text-white px-4 py-2 rounded"
              >
                Cancel
              </button>
              <button
                onClick={() => handleSaveField("status")}
                className="bg-blue-600 text-white px-4 py-2 rounded"
                disabled={isSaving}
              >
                {isSaving ? "Saving..." : "Save Changes"}
              </button>
            </div>
          </div>
        ) : (
          <span className="flex items-center gap-2">
            {formData.status}
            {formData.status === "Rejected" && (
              <span className="text-sm text-gray-600">
                ({formData.rejectionReason || "N/A"})
              </span>
            )}
            {editable && (
              <FaEdit
                className="text-blue-600 cursor-pointer ml-2"
                onClick={() => setEditField("status")}
              />
            )}
          </span>
        )}
      </div>
    </section>
  );
};

export default DrivingLicenseDetails;
