import React, { useState } from "react";
import {
  FaIdCard,
  FaEdit,
  FaCheck,
  FaTimes,
  FaUpload,
  FaSpinner,
} from "react-icons/fa";
import API from "../config/config";
import axios from "axios";

const PANDetails = ({ pan, driverId, editable }) => {
  const token = localStorage.getItem("adminToken");

  //  These must always run regardless of any conditions
  const [editField, setEditField] = useState("");
  const [uploadingSide, setUploadingSide] = useState(null);
  const [formData, setFormData] = useState({
    holderName: pan?.holderName || "",
    panNumber: pan?.panNumber || "",
    status: pan?.status || "",
    rejectionReason: pan?.rejectionReason || "",
    panfront: pan?.panfront || "",
  });

  //  Perform the conditional return *after* hooks are defined
  const noPANData =
    !pan || (!pan.panNumber && !pan.panfront && !pan.holderName);

  if (noPANData) return null;

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((p) => ({ ...p, [name]: value }));
  };

  const handleFileChange = async (e, side) => {
    const file = e.target.files[0];
    if (!file) return;

    setUploadingSide(side);

    try {
      const formdata = new FormData();
      formdata.append("documentFor", "Driver");
      formdata.append("type", "panfront");
      formdata.append("file", file, file.name);
      formdata.append("ownerId", driverId);

      const response = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + token,
          // Do NOT set Content-Type manually
        },
        body: formdata,
      });

      if (!response.ok) {
        throw new Error(`Failed to upload ${side}`);
      }

      const data = await response.json();
      const uploadedUrl = data.document?.documentUrl;

      if (!uploadedUrl) throw new Error("Upload URL not returned.");

      //  Update the Aadhaar record in DB
      const patchPayload = {
        [side]: uploadedUrl,
      };

      await axios.patch(`${API.ADMIN.PAN_DETAILS}/${pan._id}`, patchPayload, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      //  Update local state
      setFormData((prev) => ({
        ...prev,
        [side]: uploadedUrl,
      }));
    } catch (err) {
      console.error("Upload error:", err);
      alert("Failed to upload Pancard image.");
    } finally {
      setUploadingSide(null);
    }
  };

  const handleSaveField = async (field) => {
    try {
      const payload = {};

      if (field === "status") {
        payload.status = formData.status;
        if (formData.status === "Rejected") {
          payload.rejectionReason = formData.rejectionReason;
        }
      } else {
        payload[field] = formData[field];
      }

      await axios.patch(`${API.ADMIN.PAN_DETAILS}/${pan._id}`, payload, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      setEditField("");
    } catch (err) {
      console.error("Update error:", err);
      alert("Failed to update field");
    }
  };

  return (
    <section className="p-6">
      <div className="flex items-center gap-4 mb-6">
        <FaIdCard className="text-yellow-600 text-2xl" />
        <h2 className="text-2xl font-semibold text-gray-800">PAN Details</h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Full Name */}
        <div>
          <strong>Full Name:</strong>{" "}
          {editField === "holderName" ? (
            <span className="flex gap-2 items-center">
              <input
                name="holderName"
                value={formData.holderName}
                onChange={handleChange}
                className="border px-2 py-1 rounded text-sm"
              />
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={() => handleSaveField("holderName")}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setFormData((p) => ({
                    ...p,
                    holderName: pan.holderName,
                  }));
                  setEditField("");
                }}
              />
            </span>
          ) : (
            <span className="flex items-center gap-2">
              {formData.holderName}
              {editable && (
                <FaEdit
                  className="text-blue-600 cursor-pointer"
                  onClick={() => setEditField("holderName")}
                />
              )}
            </span>
          )}
        </div>

        {/* PAN Number */}
        <div>
          <strong>PAN Number:</strong>{" "}
          {editField === "panNumber" ? (
            <span className="flex gap-2 items-center">
              <input
                name="panNumber"
                value={formData.panNumber}
                onChange={handleChange}
                className="border px-2 py-1 rounded text-sm"
              />
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={() => handleSaveField("panNumber")}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setFormData((p) => ({
                    ...p,
                    panNumber: pan.panNumber,
                  }));
                  setEditField("");
                }}
              />
            </span>
          ) : (
            <span className="flex items-center gap-2">
              {formData.panNumber}
              {editable && (
                <FaEdit
                  className="text-blue-600 cursor-pointer"
                  onClick={() => setEditField("panNumber")}
                />
              )}
            </span>
          )}
        </div>

        {/* Images */}
        <div className="md:col-span-2">
          <strong className="block mb-2">PAN Card Images:</strong>
          <div className="flex gap-8 flex-wrap">
            {["panfront"].map((side) => (
              <div key={side} className="flex flex-col items-start">
                <span className="font-medium mb-1 capitalize">
                  {side === "panfront" ? "Front" : "Back"}
                </span>
                <img
                  src={formData[side]}
                  alt={side}
                  className="w-72 h-44 object-cover border rounded shadow"
                />
                {editable && (
                  <label className="mt-2 cursor-pointer flex gap-2 items-center text-sm text-blue-600">
                    {uploadingSide === side ? (
                      <FaSpinner className="animate-spin" />
                    ) : (
                      <>
                        <FaUpload />
                        Change
                      </>
                    )}
                    <input
                      type="file"
                      accept="image/*"
                      className="hidden"
                      onChange={(e) => handleFileChange(e, side)}
                      disabled={uploadingSide}
                    />
                  </label>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Status Edit */}
      <div className="mt-6 border-t pt-4">
        <strong>Status:</strong>{" "}
        {editField === "status" ? (
          <div className="grid gap-4 mt-2">
            <select
              name="status"
              value={formData.status}
              onChange={handleChange}
              className="border p-2 rounded w-full"
            >
              <option value="InProgress">In Progress</option>
              <option value="Verified">Verified</option>
              <option value="Rejected">Rejected</option>
            </select>

            {formData.status === "Rejected" && (
              <textarea
                name="rejectionReason"
                value={formData.rejectionReason}
                onChange={handleChange}
                placeholder="Rejection Reason"
                className="border p-2 rounded w-full"
              />
            )}

            <div className="flex justify-between">
              <button
                type="button"
                onClick={() => {
                  setEditField("");
                  setFormData((p) => ({
                    ...p,
                    status: pan.status,
                    rejectionReason: pan.rejectionReason || "",
                  }));
                }}
                className="bg-gray-600 text-white px-4 py-2 rounded"
              >
                Cancel
              </button>
              <button
                onClick={() => handleSaveField("status")}
                className="bg-blue-600 text-white px-4 py-2 rounded"
              >
                Save Changes
              </button>
            </div>
          </div>
        ) : (
          <span className="flex items-center gap-2">
            {formData.status}
            {formData.status === "Rejected" && (
              <span className="text-sm text-gray-600">
                ({formData.rejectionReason || "N/A"})
              </span>
            )}
            {editable && (
              <FaEdit
                className="text-blue-600 cursor-pointer ml-2"
                onClick={() => setEditField("status")}
              />
            )}
          </span>
        )}
      </div>
    </section>
  );
};

export default PANDetails;
