import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { ArrowLeft } from "lucide-react"; // Optional: or use any icon
import API from "../../config/config";

const UploadProfilePic = () => {
  const [file, setFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [message, setMessage] = useState("");
  const [previewUrl, setPreviewUrl] = useState(null);
  const navigate = useNavigate();

  const handleFileChange = (e) => {
    const selected = e.target.files[0];
    setFile(selected);
    setMessage("");

    if (selected) {
      const reader = new FileReader();
      reader.onloadend = () => setPreviewUrl(reader.result);
      reader.readAsDataURL(selected);
    } else {
      setPreviewUrl(null);
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setMessage("Please select a file first.");
      return;
    }

    setUploading(true);
    setMessage("");

    try {
      const token = localStorage.getItem("userPublicToken");
      const formdata = new FormData();
      formdata.append("type", "profilepic");
      formdata.append("documentFor", "Driver");
      formdata.append("file", file);

      const response = await fetch(API.ADMIN.DOCUMENT_UPLOAD, {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: formdata,
      });

      const result = await response.json();
      if (response.ok) {
        setMessage(" Profile picture uploaded successfully.");
        console.log(result);
        // Optional: navigate back after a short delay
        setTimeout(() => navigate("/user-dashboard"), 1000);
      } else {
        setMessage(result.message || " Upload failed.");
      }
    } catch (err) {
      console.error(err);
      setMessage(" An error occurred while uploading.");
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="max-w-3xl mx-auto bg-white border border-gray-300 rounded-xl shadow-xl p-8 mt-10">
      {/* Header with back arrow */}
      <div className="flex items-center mb-6">
        <button
          onClick={() => navigate("/user-dashboard")}
          className="mr-3 text-gray-700 hover:text-indigo-600"
        >
          <ArrowLeft className="w-6 h-6" />
        </button>
        <h2 className="text-xl font-semibold text-gray-800">
          Upload Profile Picture
        </h2>
      </div>

      {/* Image preview */}
      {previewUrl && (
        <div className="mb-4">
          <img
            src={previewUrl}
            alt="Preview"
            className="w-32 h-32 object-cover rounded-full border border-gray-300 mx-auto"
          />
        </div>
      )}

      <input
        type="file"
        accept="image/*"
        onChange={handleFileChange}
        className="mb-4 w-full border border-gray-300 rounded px-3 py-2"
      />

      <button
        onClick={handleUpload}
        disabled={uploading}
        className="w-full bg-indigo-600 text-white py-2 rounded-lg hover:bg-indigo-700 transition"
      >
        {uploading ? "Uploading..." : "Upload"}
      </button>

      {message && (
        <p className="mt-4 text-center text-sm text-gray-700">{message}</p>
      )}
    </div>
  );
};

export default UploadProfilePic;
