import React from "react";

const TrainingVideos = ({ videos, verified }) => {
  return (
    <div className="px-6 py-4 border-t border-gray-200">
      <h2 className="text-2xl font-semibold mb-4">Training Videos</h2>
      {verified ? (
        <p className="mb-4 text-green-600 font-medium">Training Verified </p>
      ) : (
        <p className="mb-4 text-yellow-600 font-medium">Training Pending </p>
      )}

      {videos.map((video, index) => (
        <div
          key={video._id || index}
          className="mb-8 bg-gray-50 p-4 rounded-lg shadow-sm"
        >
          <h3 className="text-lg font-medium mb-2">
            Car Type: {video.carType || "N/A"}
          </h3>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {video.transmission_video && (
              <div>
                <label className="font-semibold text-sm block mb-1">
                  Transmission
                </label>
                <video
                  controls
                  className="w-full h-40 rounded border"
                  src={video.transmission_video}
                />
              </div>
            )}

            {video.front_drive_video && (
              <div>
                <label className="font-semibold text-sm block mb-1">
                  Front Drive
                </label>
                <video
                  controls
                  className="w-full h-40 rounded border"
                  src={video.front_drive_video}
                />
              </div>
            )}

            {video.reverse_parking_video && (
              <div>
                <label className="font-semibold text-sm block mb-1">
                  Reverse Parking
                </label>
                <video
                  controls
                  className="w-full h-40 rounded border"
                  src={video.reverse_parking_video}
                />
              </div>
            )}
          </div>

          {video.uploadedAt && (
            <p className="mt-4 text-xs text-gray-500">
              Uploaded: {new Date(video.uploadedAt).toLocaleString()}
            </p>
          )}
        </div>
      ))}
    </div>
  );
};

export default TrainingVideos;
