import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { CSVLink } from "react-csv";
import DataTable from "../components/DataTable";
import API from "../config/config";

const Rides = () => {
  const [rides, setRides] = useState([]);
  const [filteredRides, setFilteredRides] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [loading, setLoading] = useState(true);
  const { status } = useParams();

  // Fetch rides based on status
  useEffect(() => {
    const fetchRides = async () => {
      setLoading(true);
      setRides([]); // Clear existing rides
      try {
        const token = localStorage.getItem("adminToken");
        const endpoint = status.toUpperCase();
        const response = await axios.get(API.ADMIN[`RIDES_${endpoint}_LIST`], {
          headers: {
            Authorization: `Bearer ${token}`,
            "Cache-Control": "no-cache", // Prevent caching
          },
        });
        setRides(response.data.rides);
        setFilteredRides(response.data.rides); // Initialize filtered data
      } catch (error) {
        console.error("Error fetching rides:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchRides();
  }, [status]);

  // Filter rides based on selected date range
  const handleFilter = () => {
    if (!startDate || !endDate) {
      alert("Please select valid date ranges to filter!");
      return;
    }

    const filteredData = rides.filter((ride) => {
      const rideDate = new Date(ride.createdAt);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return rideDate >= start && rideDate <= end;
    });

    setFilteredRides(filteredData);
  };

  // Clear filter and reset the state
  const handleClearFilter = () => {
    setStartDate("");
    setEndDate("");
    setFilteredRides(rides); // Reset to the original rides list
  };

  // Handle ride deletion
  const handleDelete = async (rideId) => {
    try {
      const token = localStorage.getItem("adminToken");
      await axios.delete(`${API.ADMIN.RIDE_DELETE}/${rideId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      setFilteredRides(filteredRides.filter((ride) => ride._id !== rideId));
    } catch (error) {
      console.error("Error deleting ride:", error);
    }
  };

  // Table column definitions
  const columns = [
    { Header: "RIDE ID", accessor: "RideId" },
    { Header: "Pickup Address", accessor: "PickupAddress" },
    { Header: "Dropoff Address", accessor: "DropoffAddress" },
    { Header: "Customer Name", accessor: "CustomerName" },
    { Header: "Driver Name", accessor: "DriverName" },
    { Header: "Fare (INR)", accessor: "Fare" },
    { Header: "Status", accessor: "Status" },
    { Header: "Created At", accessor: "createdAt" },
  ];

  const csvHeaders = [
    { label: "RIDE ID", key: "RideId" },
    { label: "Pickup Address", key: "PickupAddress" },
    { label: "Dropoff Address", key: "DropoffAddress" },
    { label: "Customer Name", key: "CustomerName" },
    { label: "Customer Phone", key: "CustomerPhone" },
    { label: "Customer Email", key: "CustomerEmail" },
    { label: "Driver Name", key: "DriverName" },
    { label: "Driver Phone", key: "DriverPhone" },
    { label: "Driver Email", key: "DriverEmail" },
    { label: "Vehicle Type", key: "VehicleType" },
    { label: "Distance", key: "Distance" },
    { label: "Fare", key: "Fare" },
    { label: "Status", key: "Status" },
    { label: "Payment Status", key: "PaymentStatus" },
    { label: "Payment Method", key: "PaymentMethod" },
    { label: "Payable Amount", key: "payableAmount" },
    { label: "Cashback Used", key: "cashbackUsed" },
    { label: "Created At", key: "createdAt" },
  ];

  return (
    <div className="p-6 bg-gray-100 min-h-screen">
      {/* Header Section */}
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold text-gray-800">Rides</h1>
        <CSVLink
          data={filteredRides}
          headers={csvHeaders}
          filename="rides.csv"
          className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition"
        >
          Export to CSV
        </CSVLink>
      </div>

      {/* Filter Section */}
      <div className="bg-white shadow-md rounded-lg px-6 py-4 mb-6 border border-gray-200">
        <div className="flex items-center space-x-4">
          <div>
            <label className="text-sm text-gray-600 block mb-1">
              Start Date
            </label>
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>
          <div>
            <label className="text-sm text-gray-600 block mb-1">End Date</label>
            <input
              type="date"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>
          <button
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg shadow-md transition duration-200 ease-in-out"
            onClick={handleFilter}
          >
            Apply Filter
          </button>
          <button
            className="bg-gray-400 hover:bg-gray-500 text-white px-4 py-2 rounded-lg shadow-md transition duration-200 ease-in-out"
            onClick={handleClearFilter}
          >
            Clear Filter
          </button>
        </div>
      </div>

      {/* Rides Table Section */}
      <div className="bg-white shadow-md rounded-lg px-4 py-6 border border-gray-200">
        <DataTable
          key={status} // Forces re-render when status changes
          data={filteredRides}
          columns={columns}
          loading={loading}
          editPath="/rides/edit"
          detailPath="/rides/detail"
          deleteHandler={handleDelete}
        />
      </div>
    </div>
  );
};

export default Rides;
