import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import API from "../config/config";
import Loginleftimg from "../images/loginleft.jpg";
import LogoIcon from "../images/logo.webp";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    console.log(email);
    e.preventDefault();
    try {
      const response = await axios.post(API.ADMIN.LOGIN, {
        emailId: email,
        password: password,
      });

      http: console.log(response);
      const { token } = response.data;

      // Save token in local storage or cookie
      localStorage.setItem("adminToken", token);
      navigate("/");
    } catch (error) {
      setError("Invalid login credentials");
    }
  };

  return (
    <div
      className={`min-h-screen flex items-center justify-center bg-gray-100 `}
    >
      <div class="grid md:grid-cols-3 grid-cols-1 p-10 h-screen">
        <div className="md:col-span-2 bg-white rounded shadow-md w-full invisible md:visible">
          <img
            src={Loginleftimg}
            className="w-full h-full m-auto mb-2"
            alt="logo icon"
          />
        </div>
        <div className="bg-white p-4 rounded shadow-md w-full">
          <img
            src={LogoIcon}
            className="size-24 m-auto mb-3 mt-2"
            alt="logo icon"
          />
          <h2 className="text-2xl font-bold mb-6 text-center">Admin Login</h2>
          {error && <p className="text-red-500 mb-4">{error}</p>}
          <form onSubmit={handleLogin}>
            <input
              type="email"
              className="w-full mb-6 p-2 border border-gray-300 rounded-3xl"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <input
              type="password"
              className="w-full mb-6 p-2 border border-gray-300 rounded-3xl"
              placeholder="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
            <button className="w-full bg-blue-500 text-white p-2 rounded-3xl">
              Login
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Login;
