import React, { useEffect, useState } from "react";
import DataTable from "../components/DataTable";
import { useNavigate } from "react-router-dom";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

const Admins = () => {
  const navigate = useNavigate();
  const [admins, setAdmins] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAdmins = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(API.ADMIN.ADMIN_LIST, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setAdmins(response.data.admins);
      } catch (error) {
        console.error("Error fetching employees:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchAdmins();
  }, []);

  const handleDelete = async (adminId) => {
    try {
      await axiosInstance.delete(`${API.ADMIN.ADMIN_DELETE}/${adminId}`);
      setAdmins(admins.filter((employee) => admins._id !== adminId));
    } catch (error) {
      console.error("Error deleting employee:", error);
    }
  };

  const columns = [
    { Header: "Admin ID", accessor: "employeeCode" },
    {
      Header: "Name",
      accessor: "fullName",
    },
    { Header: "Role", accessor: "role" },
    { Header: "Phone", accessor: "phone" },
    { Header: "Email", accessor: "emailId" },
  ];

  return (
    <div className="px-4 py-6">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Admins</h1>
        <button
          onClick={() => navigate("/admins/add")}
          className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition"
        >
          Add Admin
        </button>
      </div>
      <DataTable
        data={admins}
        columns={columns}
        loading={loading}
        editPath="/admins/edit"
        detailPath="/admins/detail"
        deleteHandler={handleDelete}
      />
    </div>
  );
};

export default Admins;
