import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import DataTable from "../components/DataTable";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

const Employees = () => {
  const navigate = useNavigate();
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchEmployees = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(API.ADMIN.EMPLOYEES_LIST, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });
        setEmployees(response.data.employees);
      } catch (error) {
        console.error("Error fetching employees:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchEmployees();
  }, []);

  const handleDelete = async (employeeId) => {
    try {
      await axiosInstance.delete(`${API.ADMIN.EMPLOYEE_DELETE}/${employeeId}`);
      setEmployees(employees.filter((employee) => employee._id !== employeeId));
    } catch (error) {
      console.error("Error deleting employee:", error);
    }
  };

  const columns = [
    { Header: "Employee ID", accessor: "employeeCode" },
    {
      Header: "Name",
      accessor: "fullName",
    },
    { Header: "Phone", accessor: "phone" },
    { Header: "Email", accessor: "emailId" },
  ];

  return (
    <div className="px-4 py-6">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">Employees</h1>
        <button
          onClick={() => navigate("/employees/add")}
          className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 transition"
        >
          Add Employee
        </button>
      </div>
      <DataTable
        data={employees}
        columns={columns}
        loading={loading}
        editPath="/employees/edit"
        detailPath="/employees/detail"
        deleteHandler={handleDelete}
      />
    </div>
  );
};

export default Employees;
