import React from "react";
import { FaWallet } from "react-icons/fa";

const BankDetails = ({ bank }) => {
  //  Guard against missing or empty bank object
  const noBankData =
    !bank ||
    (!bank.accountNumber &&
      !bank.ifsc &&
      !bank.beneficiaryName &&
      !bank.verificationStatus);

  if (noBankData) return null;

  return (
    <section className="p-6">
      <div className="flex items-center gap-4 mb-6">
        <FaWallet className="text-red-600 text-2xl" />
        <h2 className="text-2xl font-semibold text-gray-800">Bank Details</h2>
      </div>
      {bank.verified ? (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <p>
            <strong>Account Number:</strong> {bank.accountNumber}
          </p>
          <p>
            <strong>IFSC:</strong> {bank.ifsc}
          </p>
          <p>
            <strong>Beneficiary Name:</strong> {bank.beneficiaryName}
          </p>
          <p>
            <strong>Status:</strong> {bank.verificationStatus}
          </p>
        </div>
      ) : (
        <p className="text-red-600">Bank Not Verified</p>
      )}
    </section>
  );
};

export default BankDetails;
