import React, { useState } from "react";
import {
  FaIdCard,
  FaEdit,
  FaCheck,
  FaTimes,
  FaUpload,
  FaSpinner,
} from "react-icons/fa";
import API from "../config/config";
import axios from "axios";

const AadhaarDetails = ({ aadhaar, driverId, editable }) => {
  const token = localStorage.getItem("adminToken");

  //  Hooks must come first
  const [editField, setEditField] = useState("");
  const [uploadingSide, setUploadingSide] = useState(null);
  const [formData, setFormData] = useState({
    fullName: aadhaar?.fullName || "",
    aadhaarNumber: aadhaar?.aadhaarNumber || "",
    status: aadhaar?.status || "",
    rejectionReason: aadhaar?.rejectionReason || "",
    aadhaarfront: aadhaar?.aadhaarfront || "",
    aadhaarback: aadhaar?.aadhaarback || "",
  });

  //  Perform conditional logic AFTER hooks
  const noAadhaarData =
    !aadhaar ||
    (!aadhaar.aadhaarNumber &&
      !aadhaar.aadhaarfront &&
      !aadhaar.aadhaarback &&
      !aadhaar.fullName);

  if (noAadhaarData) return null;
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((p) => ({ ...p, [name]: value }));
  };

  const handleFileChange = async (e, side) => {
    const file = e.target.files[0];
    if (!file) return;

    setUploadingSide(side);

    try {
      const formdata = new FormData();
      formdata.append("documentFor", "Driver");
      formdata.append(
        "type",
        side === "aadhaarfront" ? "aadhaar_front" : "aadhaar_back"
      );
      formdata.append("file", file, file.name);
      formdata.append("ownerId", driverId);

      const response = await fetch(API.ADMIN.ADMINDOCUMENT_UPLOAD, {
        method: "POST",
        headers: {
          Authorization: "Bearer " + token,
          // Do NOT set Content-Type manually
        },
        body: formdata,
      });

      if (!response.ok) {
        throw new Error(`Failed to upload ${side}`);
      }

      const data = await response.json();
      const uploadedUrl = data.document?.documentUrl;

      if (!uploadedUrl) throw new Error("Upload URL not returned.");

      //  Update the Aadhaar record in DB
      const patchPayload = {
        [side]: uploadedUrl,
      };

      await axios.patch(
        `${API.ADMIN.AADHAAR_DETAILS}/${aadhaar._id}`,
        patchPayload,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      //  Update local state
      setFormData((prev) => ({
        ...prev,
        [side]: uploadedUrl,
      }));
    } catch (err) {
      console.error("Upload error:", err);
      alert("Failed to upload Aadhaar image.");
    } finally {
      setUploadingSide(null);
    }
  };

  const handleSaveField = async (field) => {
    try {
      const payload = {};
      if (field === "status") {
        payload.status = formData.status;
        if (formData.status === "Rejected") {
          payload.rejectionReason = formData.rejectionReason;
        }
      } else {
        payload[field] = formData[field];
      }

      await axios.patch(
        `${API.ADMIN.AADHAAR_DETAILS}/${aadhaar._id}`,
        payload,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      setEditField("");
    } catch (err) {
      console.error("Update error:", err);
      alert("Failed to update field");
    }
  };

  return (
    <section className="p-6">
      <div className="flex items-center gap-4 mb-6">
        <FaIdCard className="text-green-600 text-2xl" />
        <h2 className="text-2xl font-semibold text-gray-800">
          Aadhaar Details
        </h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Full Name */}
        <div>
          <strong>Full Name:</strong>{" "}
          {editField === "fullName" ? (
            <span className="flex gap-2 items-center">
              <input
                name="fullName"
                value={formData.fullName}
                onChange={handleChange}
                className="border px-2 py-1 rounded text-sm"
              />
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={() => handleSaveField("fullName")}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setFormData((p) => ({ ...p, fullName: aadhaar.fullName }));
                  setEditField("");
                }}
              />
            </span>
          ) : (
            <span className="flex items-center gap-2">
              {formData.fullName}
              {editable && (
                <FaEdit
                  className="text-blue-600 cursor-pointer"
                  onClick={() => setEditField("fullName")}
                />
              )}
            </span>
          )}
        </div>

        {/* Aadhaar Number */}
        <div>
          <strong>Aadhaar Number:</strong>{" "}
          {editField === "aadhaarNumber" ? (
            <span className="flex gap-2 items-center">
              <input
                name="aadhaarNumber"
                value={formData.aadhaarNumber}
                onChange={handleChange}
                className="border px-2 py-1 rounded text-sm"
              />
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={() => handleSaveField("aadhaarNumber")}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setFormData((p) => ({
                    ...p,
                    aadhaarNumber: aadhaar.aadhaarNumber,
                  }));
                  setEditField("");
                }}
              />
            </span>
          ) : (
            <span className="flex items-center gap-2">
              {formData.aadhaarNumber}
              {editable && (
                <FaEdit
                  className="text-blue-600 cursor-pointer"
                  onClick={() => setEditField("aadhaarNumber")}
                />
              )}
            </span>
          )}
        </div>

        {/* Aadhaar Images */}
        <div className="md:col-span-2">
          <strong className="block mb-2">Aadhaar Images:</strong>
          <div className="flex gap-8 flex-wrap">
            {["aadhaarfront", "aadhaarback"].map((side) => (
              <div key={side} className="flex flex-col items-start">
                <span className="font-medium mb-1 capitalize">
                  {side === "aadhaarfront" ? "Front" : "Back"}
                </span>
                <img
                  src={formData[side]}
                  alt={side}
                  className="w-72 h-44 object-cover border rounded shadow"
                />
                {editable && (
                  <label className="mt-2 cursor-pointer flex gap-2 items-center text-sm text-blue-600">
                    {uploadingSide === side ? (
                      <FaSpinner className="animate-spin" />
                    ) : (
                      <>
                        <FaUpload />
                        Change
                      </>
                    )}
                    <input
                      type="file"
                      accept="image/*"
                      className="hidden"
                      onChange={(e) => handleFileChange(e, side)}
                      disabled={uploadingSide}
                    />
                  </label>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Status Editor */}
      <div className="mt-6">
        <strong>Status:</strong>{" "}
        {editField === "status" ? (
          <span className="flex flex-col gap-2 mt-2">
            <select
              className="border p-1 rounded w-full max-w-xs"
              value={formData.status}
              onChange={(e) =>
                setFormData((prev) => ({
                  ...prev,
                  status: e.target.value,
                }))
              }
            >
              <option value="InProgress">In Progress</option>
              <option value="Verified">Verified</option>
              <option value="Rejected">Rejected</option>
            </select>

            {formData.status === "Rejected" && (
              <textarea
                className="border p-2 rounded w-full max-w-xs"
                placeholder="Enter rejection reason"
                value={formData.rejectionReason}
                onChange={(e) =>
                  setFormData((prev) => ({
                    ...prev,
                    rejectionReason: e.target.value,
                  }))
                }
              />
            )}

            <div className="flex gap-2">
              <FaCheck
                className="text-green-600 cursor-pointer text-lg"
                onClick={() => handleSaveField("status")}
              />
              <FaTimes
                className="text-red-600 cursor-pointer text-lg"
                onClick={() => {
                  setFormData((p) => ({
                    ...p,
                    status: aadhaar.status,
                    rejectionReason: aadhaar.rejectionReason || "",
                  }));
                  setEditField("");
                }}
              />
            </div>
          </span>
        ) : (
          <span className="flex items-center gap-2">
            {formData.status}
            {formData.status === "Rejected" && (
              <span className="text-sm text-gray-600">
                ({formData.rejectionReason || "N/A"})
              </span>
            )}
            {editable && (
              <FaEdit
                className="text-blue-600 cursor-pointer"
                onClick={() => setEditField("status")}
              />
            )}
          </span>
        )}
      </div>
    </section>
  );
};

export default AadhaarDetails;
