import React, { useEffect, useState } from "react";
import { CSVLink } from "react-csv";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";
import DataTable from "../components/DataTable";
import { useLocation, useNavigate } from "react-router-dom";
import { Link } from "react-router-dom";

const Drivers = () => {
  const [drivers, setDrivers] = useState([]);
  const [filteredDrivers, setFilteredDrivers] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [loading, setLoading] = useState(true);

  const navigate = useNavigate();

  const location = useLocation();
  const queryParams = new URLSearchParams(location.search);
  const statusFilter = queryParams.get("status"); // e.g. 'Pending'

  // Inside useEffect
  useEffect(() => {
    const fetchDrivers = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(
          `${API.ADMIN.DRIVERS_LIST}${
            statusFilter ? `?status=${statusFilter}` : ""
          }`,
          {
            headers: { Authorization: `Bearer ${token}` },
          }
        );

        console.log(response.data.drivers);

        const formattedDrivers = response.data.drivers.map((driver) => ({
          ...driver,
          createdAt: formatDateTime(driver.createdAt),
        }));
        console.log(formattedDrivers);
        setDrivers(formattedDrivers);
        setFilteredDrivers(formattedDrivers);
      } catch (error) {
        console.error("Error fetching driver data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchDrivers();
  }, [statusFilter]);

  const formatDateTime = (isoString) => {
    const date = new Date(isoString);
    const options = {
      timeZone: "Asia/Kolkata", // adjust to your preferred timezone
      year: "numeric",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
      hour12: false,
    };

    const formatted = new Intl.DateTimeFormat("en-GB", options).format(date);
    const [datePart, timePart] = formatted.split(", ");
    return `${datePart.replace(/\//g, "-")} ${timePart}`;
  };

  const handleFilter = () => {
    if (!startDate || !endDate) {
      alert("Please select valid date ranges to filter!");
      return;
    }

    const filteredData = drivers.filter((driver) => {
      const createdAt = new Date(driver.createdAt);
      const start = new Date(startDate);
      const end = new Date(endDate);
      return createdAt >= start && createdAt <= end;
    });

    setFilteredDrivers(filteredData);
  };

  const handleDelete = async (driverId) => {
    try {
      await axiosInstance.delete(`${API.ADMIN.DRIVER_DELETE}/${driverId}`);
      setFilteredDrivers(
        filteredDrivers.filter((driver) => driver._id !== driverId)
      );
    } catch (error) {
      console.error("Error deleting driver:", error);
    }
  };

  const csvHeaders = [
    { label: "Date", key: "createdAt" },
    { label: "Driver Id", key: "code" },
    { label: "Phone", key: "phone" },
    { label: "Name", key: "fullName" },
    { label: "Wallet Balance", key: "walletBalance" },
    { label: "Created At", key: "createdAt" },
    { label: "Pincode", key: "pincode" },
  ];

  const columns = [
    { Header: "Date", accessor: "createdAt" },
    { Header: "City", accessor: "drivingChoice.city" },
    { Header: "Driver Id", accessor: "code" },
    { Header: "Full Name", accessor: "fullName" },
    { Header: "Mobile Number", accessor: "phone" },
    { Header: "DL Number", accessor: "dlNumber" },
    { Header: "Aaadhaar Number", accessor: "aadhaarNumber" },
    { Header: "Pan Number", accessor: "panNumber" },
    { Header: "Status", accessor: "status" },
    {
      Header: "Assigned To",
      accessor: "assignee",
      Cell: ({ value }) =>
        value && value._id ? (
          <Link
            to={`/employees/profile/${value._id}`}
            className="text-blue-600 hover:underline"
          >
            {value.name}
          </Link>
        ) : (
          "-"
        ),
    },
  ];

  const handleAddDriver = () => {
    navigate("/drivers/add");
  };

  return (
    <div className="container mx-auto px-6 py-8 bg-gray-100 min-h-screen">
      {/* Header Section */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-6">
        <h2 className="text-3xl font-bold text-gray-800">Driver Management</h2>
        <div className="flex flex-col sm:flex-row gap-2">
          <button
            onClick={handleAddDriver}
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg shadow-md"
          >
            + Add Driver
          </button>
        </div>
      </div>

      {/* Filter Section */}
      <div className="bg-white shadow-md rounded-lg px-6 py-4 mb-6 border border-gray-200">
        <div className="flex items-center space-x-4">
          {/* Single-line Date Pickers */}
          <div>
            <label className="text-sm text-gray-600 block mb-1">
              Start Date
            </label>
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>

          <div>
            <label className="text-sm text-gray-600 block mb-1">End Date</label>
            <input
              type="date"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              className="border px-3 py-2 rounded-lg shadow-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
          </div>

          {/* Apply Filter Button */}
          <button
            className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg shadow-md transition duration-200 ease-in-out focus:outline-none"
            onClick={handleFilter}
          >
            Apply Filter
          </button>
        </div>
      </div>

      {/* Driver Table Section */}
      <div className="bg-white shadow-md rounded-lg px-4 py-6 border border-gray-200">
        <DataTable
          data={filteredDrivers}
          columns={columns}
          loading={loading}
          addLink="/drivers/add"
          detailPath="/drivers/detail"
          // deleteHandler={handleDelete}
        />
      </div>
    </div>
  );
};

export default Drivers;
