import React, { useState } from "react";
import { Link } from "react-router-dom";
import LogoIcon from "../images/logo.webp";
import { useProfile } from "../context/ProfileContext";

import {
  FaTachometerAlt,
  FaUsers,
  FaChevronDown,
  FaChevronUp,
  FaHeadset,
  FaTags,
} from "react-icons/fa";

const AdminSidebar = ({ open }) => {
  const { profile } = useProfile();

  const isSuperAdmin = profile?.role?.slug === "superadmin";
  const [activeMenu, setActiveMenu] = useState(null);

  const toggleMenu = (menu) => {
    setActiveMenu(activeMenu === menu ? null : menu);
  };

  return (
    <aside
      className={`mobileview overflow-y-auto no-scrollbar w-64 bg-slate-700 text-black h-screen p-5 transform transition-transform duration-300 fixed md:relative z-10 ${
        open ? "hidden mobileshow" : "translate-x-0"
      }`}
    >
      {/* Sidebar */}
      <div className={``}>
        <img src={LogoIcon} className="size-24 m-auto mb-2" alt="logo icon" />

        <nav className="space-y-4">
          <Link
            to="/"
            onClick={() => toggleMenu("Dashboard")}
            className={`flex items-center mb-4 px-2 py-2 hover:text-yellow ${
              activeMenu === "Dashboard"
                ? "bg-yellow-300 font-semibold text-black"
                : "text-white"
            }`}
          >
            <FaTachometerAlt className="mr-3" />
            <span>Dashboard</span>
          </Link>

          {/* Users Menu */}
          <div>
            <div
              onClick={() => toggleMenu("users")}
              className={`flex items-center mb-4 px-2 py-2 cursor-pointer ${
                activeMenu === "users"
                  ? "bg-yellow-300 font-semibold"
                  : "text-white"
              }`}
            >
              <FaUsers className="mr-3" />
              <span>Users</span>
              <span className="ml-auto">
                {activeMenu === "users" ? <FaChevronUp /> : <FaChevronDown />}
              </span>
            </div>
            {activeMenu === "users" && (
              <div className="ml-6 space-y-3 mt-2">
                {isSuperAdmin && (
                  <Link
                    to="/admins"
                    className="flex items-center text-white hover:text-yellow"
                  >
                    <span>Admins</span>
                  </Link>
                )}
                {isSuperAdmin && (
                  <Link
                    to="/employees"
                    className="flex items-center text-white hover:text-yellow"
                  >
                    <span>Employees</span>
                  </Link>
                )}
                {isSuperAdmin && (
                  <Link
                    to="/users"
                    className="flex items-center text-white hover:text-yellow"
                  >
                    <span>Users</span>
                  </Link>
                )}
                <Link
                  to="/drivers"
                  className="flex items-center text-white hover:text-yellow"
                >
                  <span>Drivers</span>
                </Link>
              </div>
            )}
          </div>
          {isSuperAdmin && (
            <Link
              to="/roles"
              onClick={() => toggleMenu("Roles")}
              className={`flex items-center mb-4 px-2 py-2 hover:text-yellow ${
                activeMenu === "Roles"
                  ? "bg-yellow-300 font-semibold text-black"
                  : "text-white"
              }`}
            >
              <FaUsers className="mr-3" />
              <span>Roles</span>
            </Link>
          )}
          <Link
            to="/issues"
            onClick={() => toggleMenu("Issues")}
            className={`flex items-center mb-4 px-2 py-2 hover:text-yellow ${
              activeMenu === "Issues"
                ? "bg-yellow-300 font-semibold text-black"
                : "text-white"
            }`}
          >
            <FaHeadset className="mr-3" />
            <span>Issues</span>
          </Link>
          {isSuperAdmin && (
            <Link
              to="/ratecard"
              onClick={() => toggleMenu("Rete Card")}
              className={`flex items-center mb-4 px-2 py-2 hover:text-yellow ${
                activeMenu === "Rate Card"
                  ? "bg-yellow-300 font-semibold text-black"
                  : "text-white"
              }`}
            >
              <FaTags className="mr-3" />
              <span>Rate Card</span>
            </Link>
          )}
        </nav>
      </div>
    </aside>
  );
};

export default AdminSidebar;
