import React, { useState } from "react";
import TextInput from "../components/TextInput"; // Assuming TextInput is created
import API from "../config/config";

const Settings = () => {
  const [activeTab, setActiveTab] = useState("razorpay"); // Default active tab
  const [razorpaySettings, setRazorpaySettings] = useState({
    key: "",
    secret: "",
  });
  const [s3Settings, setS3Settings] = useState({
    bucket: "",
    accessKey: "",
    secretKey: "",
  });

  const handleChange = (e, setState) => {
    const { name, value } = e.target;
    setState((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e, settings, endpoint) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem("adminToken");
      const response = await fetch(endpoint, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(settings),
      });
      if (response.ok) {
        alert("Settings saved successfully!");
      } else {
        console.error("Failed to save settings");
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  return (
    <div className="container mx-auto p-6 bg-gray-100 min-h-screen">
      <h2 className="text-3xl font-bold text-center mb-6">Settings</h2>
      <div className="flex space-x-4 border-b-2 pb-2">
        <button
          onClick={() => setActiveTab("razorpay")}
          className={`px-4 py-2 font-semibold ${
            activeTab === "razorpay"
              ? "border-b-4 border-yellow-500 text-yellow-500"
              : "text-gray-500"
          }`}
        >
          Razorpay Settings
        </button>
        <button
          onClick={() => setActiveTab("s3")}
          className={`px-4 py-2 font-semibold ${
            activeTab === "s3"
              ? "border-b-4 border-yellow-500 text-yellow-500"
              : "text-gray-500"
          }`}
        >
          S3 Details
        </button>
        <button
          onClick={() => setActiveTab("other")}
          className={`px-4 py-2 font-semibold ${
            activeTab === "other"
              ? "border-b-4 border-yellow-500 text-yellow-500"
              : "text-gray-500"
          }`}
        >
          Other Settings
        </button>
      </div>

      <div className="mt-6 bg-white shadow-lg rounded-lg p-6">
        {/* Razorpay Settings */}
        {activeTab === "razorpay" && (
          <form
            onSubmit={(e) =>
              handleSubmit(e, razorpaySettings, API.ADMIN.RAZORPAY_SETTINGS)
            }
          >
            <h3 className="text-xl font-semibold mb-4">Razorpay Settings</h3>
            <TextInput
              name="key"
              label="Razorpay Key"
              value={razorpaySettings.key}
              onChange={(e) => handleChange(e, setRazorpaySettings)}
            />
            <TextInput
              name="secret"
              label="Razorpay Secret"
              value={razorpaySettings.secret}
              onChange={(e) => handleChange(e, setRazorpaySettings)}
            />
            <button
              type="submit"
              className="mt-4 bg-yellow-300 text-white font-semibold py-3 px-6 rounded-lg hover:bg-yellow-400 transition"
            >
              Save
            </button>
          </form>
        )}

        {/* S3 Settings */}
        {activeTab === "s3" && (
          <form
            onSubmit={(e) => handleSubmit(e, s3Settings, API.ADMIN.S3_SETTINGS)}
          >
            <h3 className="text-xl font-semibold mb-4">S3 Details</h3>
            <TextInput
              name="bucket"
              label="Bucket Name"
              value={s3Settings.bucket}
              onChange={(e) => handleChange(e, setS3Settings)}
            />
            <TextInput
              name="accessKey"
              label="Access Key"
              value={s3Settings.accessKey}
              onChange={(e) => handleChange(e, setS3Settings)}
            />
            <TextInput
              name="secretKey"
              label="Secret Key"
              value={s3Settings.secretKey}
              onChange={(e) => handleChange(e, setS3Settings)}
            />
            <button
              type="submit"
              className="mt-4 bg-yellow-300 text-white font-semibold py-3 px-6 rounded-lg hover:bg-yellow-400 transition"
            >
              Save
            </button>
          </form>
        )}

        {/* Other Settings */}
        {activeTab === "other" && (
          <div>
            <h3 className="text-xl font-semibold mb-4">Other Settings</h3>
            <p className="text-gray-500">Add more settings here...</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Settings;
