import React, { useEffect, useState } from "react";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";
import DriverStatusSummaryCards from "../components/DriverStatusSummaryCards";

const Dashboard = () => {
  const [summaryData, setSummaryData] = useState({
    Pending: 0,
    Revisit: 0,
    Approved: 0,
    Rejected: 0,
    Onboarded: 0,
  });
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchSummary = async () => {
      try {
        const token = localStorage.getItem("adminToken");
        const response = await axiosInstance.get(API.ADMIN.DASHBOARD_SUMMARY, {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (response.data.success) {
          setSummaryData(response.data.data); // Use `data` as per API response
        } else {
          setError("Failed to fetch summary data");
        }
      } catch (err) {
        console.error("Dashboard summary fetch error:", err);
        setError("Error fetching dashboard summary");
      }
    };

    fetchSummary();
  }, []);

  if (error) return <p className="text-red-500 p-4">{error}</p>;

  return (
    <div className="bg-white p-5 rounded-lg shadow-lg">
      <div className="p-4">
        <h2 className="text-2xl font-bold mb-4">Driver Profile Summary</h2>
        <DriverStatusSummaryCards summaryData={summaryData} />
      </div>
    </div>
  );
};

export default Dashboard;
