import React, { useState } from "react";
import { FaUser, FaEdit, FaCheck, FaTimes } from "react-icons/fa";
import axiosInstance from "../config/axiosInstance";
import API from "../config/config";

const PersonalInformation = ({ driver, editable, onUpdated }) => {
  const [editMode, setEditMode] = useState({ name: false, phone: false });
  const [name, setName] = useState(driver.fullName);
  const [phone, setPhone] = useState(driver.phone);
  const token = localStorage.getItem("adminToken");
  const handleNameUpdate = async () => {
    try {
      await axiosInstance.patch(
        `${API.ADMIN.UPDATE_DRIVER}/${driver._id}`,
        {
          fullName: name,
        },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      setEditMode({ ...editMode, name: false });
      onUpdated();
    } catch (err) {
      alert("Failed to update name");
    }
  };

  return (
    <section className="p-6">
      <div className="flex items-center gap-4 mb-6">
        <FaUser className="text-blue-600 text-2xl" />
        <h2 className="text-2xl font-semibold text-gray-800">
          Personal Information
        </h2>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Full Name */}
        <div>
          <strong>Full Name:</strong>{" "}
          {editMode.name ? (
            <span className="flex gap-2 items-center">
              <input
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="border px-2 py-1 rounded text-sm"
              />
              <FaCheck
                className="text-green-600 cursor-pointer"
                onClick={handleNameUpdate}
              />
              <FaTimes
                className="text-red-600 cursor-pointer"
                onClick={() => {
                  setEditMode({ ...editMode, name: false });
                  setName(driver.fullName);
                }}
              />
            </span>
          ) : (
            <span className="flex items-center gap-2">
              {driver.fullName}
              {editable && (
                <FaEdit
                  className="text-blue-600 cursor-pointer"
                  onClick={() => setEditMode({ ...editMode, name: true })}
                />
              )}
            </span>
          )}
        </div>

        {/* Phone Number */}
        <div>
          <strong>Phone:</strong>{" "}
          <span className="flex items-center gap-2">{driver.phone}</span>
        </div>

        <p>
          <strong>Status:</strong> {driver.status}
        </p>
        <p>
          <strong>ID:</strong> {driver.code}
        </p>
        <p>
          <strong>Wallet Balance:</strong> {driver.walletBalance}
        </p>
        <p>
          <strong>Rating:</strong> {driver.rating}
        </p>
        <p>
          <strong>Created At:</strong>{" "}
          {new Date(driver.createdAt).toLocaleString()}
        </p>
        <p>
          <strong>Updated At:</strong>{" "}
          {new Date(driver.updatedAt).toLocaleString()}
        </p>
      </div>
    </section>
  );
};

export default PersonalInformation;
